.art-potreb-details {
  margin-block-end: clamp(80px, 3.8235rem + 5.8824vw, 130px);
  &__inner {
    display: flex;
    column-gap: clamp(15px, 0.5846rem + 1.7647vw, 30px);
    column-gap: 20px
  }
  &__item {
    flex-grow: 0;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    inline-size: clamp(214px, 4.9985rem + 41.8824vw, 570px);
  }
  &__item.first {
    justify-content: center;
    max-inline-size: 470px;
    font-size: clamp(24px, 0.5588rem + 4.7059vw, 64px);
    line-height: 1.3;
    text-transform: uppercase;
    visibility: hidden;
    opacity: 0;
    transform: translateX(-100%);
  }
  &__item.odd {
    .art-potreb-details__img {
      order: 1;
      margin-block-end: 0;
    }
    .art-potreb-details__desc {
      align-self: unset;
      order: 0;
      margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
    }
  }
  &__img {
    aspect-ratio: 1.7168 / 1;
    inline-size: 100%;
    margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
    img {
      --h: 100%;
      --skew: calc(var(--h) / 8);
      inline-size: 100%;
      block-size: 100%;
      clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, var(--skew) 100%);
    }
  }
  &__desc {
    align-self: end;
    max-inline-size: 470px;
  }
  &__heading {
    margin-block-end: 10px;
  }
  &__desc>p+p {
    margin-block-start: 20px;
  }
}

@media (max-width: 768px) {
  .art-potreb-details {
    margin-inline: 15px;
    &__inner {
      flex-direction: column;
      gap: 20px 0;
    }
    &__item {
      inline-size: 100%;
    }
    &__item.first {
      justify-content: center;
      max-inline-size: unset;
      visibility: unset;
      opacity: unset;
      transform: unset;
    }
    &__item.odd {
      .art-potreb-details__img {
        order: unset;
        margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
      }
      .art-potreb-details__desc {
        align-self: unset;
        order: unset;
        margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
      }
    }
    &__img {
      max-inline-size: 470px;
      inline-size: 100%;
    }
    &__desc {
      align-self: unset;
      max-inline-size: unset;
      margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
    }
  }
}
