.art-chars {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin-block-end: clamp(60px, 3.0441rem + 3.5294vw, 90px);
  &__features {}
  &__title {
    margin-block-end: 20px;
  }
  &__table {
    inline-size: 100%;
  }
  &__row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: clamp(15px, 0.5846rem + 1.7647vw, 30px);
    inline-size: 100%;
  }
  &__row:nth-child(2n-1) {
    background-color: var(--clr-bg-gray);
  }
  span {
    display: inline-block;
    padding-block: 20px;
    font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
  }
  &__row span:first-child {
    padding-inline-start: clamp(10px, 0.1544rem + 2.3529vw, 30px);
    font-weight: var(--fw-b);
  }
}

@media (max-width: 1100px) {
  .art-chars {
    &__row {
      display: flex;
      justify-content: space-between;
    }
    &__row span:last-child {
      padding-inline-end: clamp(10px, 0.1544rem + 2.3529vw, 30px);
    }
  }
}

@media (max-width: 768px) {
  .art-chars {
    grid-template-columns: 1fr;
    column-gap: unset;
    row-gap: 40px;
  }
}
