.prof-categ-products {}

.prof-categ-product {
  padding-block: clamp(30px, 1.1691rem + 3.5294vw, 60px);
  &__title {
    margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
  }
}

.prof-categ-product.even {
  background-color: var(--clr-bg-light);
  border-block-start: 1px solid var(--clr-border);
  border-block-end: 1px solid var(--clr-border);
}

.tabs-acc.cpt {
  .tabs-acc__content {
    padding-block-end: 0;
    @media (max-width: 500px) {
      border-block-start: 1px solid var(--clr-border);
    }
  }
  .tabs-acc__content-inner {
    padding-inline-end: 0;
  }
  .cpt-desc {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 0 30px;
    &__img {
      grid-column: 1 / span 4;
      grid-row: 1 / span 1;
    }
    &__content {
      grid-column: 5 / -1;
      grid-row: 1 / span 1;
    }
    .content-extra {
      grid-row: 2 / span 1;
    }
    &__title {
      margin-block-end: 20px;
    }
    &__text {
      overflow: hidden;
      margin-block-end: 30px;
    }
    &__text>p+p {
      margin-block-start: 20px;
    }
  }
  @media (max-width: 1000px) {
    .cpt__nav {
      background-color: transparent;
    }
  }
  @media (max-width: 768px) {
    .cpt-desc {
      gap: unset;
      &__img {
        grid-column: 1 / -1;
        max-inline-size: 470px;
        margin-block-end: 30px;
      }
      &__content {
        grid-column: 1 / -1;
        grid-row: 2 / span 1;
      }
      .content-extra {
        grid-row: 3 / span 1;
      }
    }
  }
  @media (max-width: 500px) {
    .cpt__nav {
      border-block-end: 0;
      .tabs-acc__tab {
        flex: 0 0 auto;
      }
    }
  }
  @media (max-width: 390px) {
    .cpt__nav {
      overflow: visible;
      padding-inline-end: 0;
    }
  }
  .cpt-chars {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
    &__img1 {
      grid-column: 1 / span 7;
    }
    &__img2 {
      grid-column: 8 / -1;
      justify-self: end;
      max-inline-size: 370px;
    }
  }
  @media (max-width: 768px) {
    .cpt-chars {
      column-gap: unset;
      row-gap: 30px;
      &__img1 {
        grid-column: 1 / -1;
        grid-row: 1 / span 1;
        justify-self: center;
        max-inline-size: 470px;
      }
      &__img2 {
        grid-column: 1 / -1;
        grid-row: 2 / span 1;
        justify-self: center;
      }
    }
  }
  .cpt-benefs {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 0 30px;
    &__img {
      grid-column: 1 / span 5;
    }
    &__list {
      grid-column: 6 / -1;
      justify-self: end;
      max-inline-size: 570px;
      margin: 0;
      padding: 0;
      list-style: none;
    }
    &__item {
      margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
    }
    &__item::before {
      content: '';
      display: block;
      inline-size: 36px;
      block-size: 10px;
      margin-block-end: 10px;
      transform: skew(20deg);
      background-color: var(--clr-accent);
    }
    &__heading {
      margin-block-end: 15px;
    }
    &__desc p {
      font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
    }
    &__desc>p+p {
      margin-block-start: 20px;
    }
  }
  @media (max-width: 768px) {
    .cpt-benefs {
      column-gap: unset;
      &__img {
        grid-column: 1 / -1;
        grid-row: 1 / span 1;
        justify-self: center;
        max-inline-size: 470px;
        margin-block-end: 30px;
      }
      &__list {
        grid-column: 1 / -1;
        grid-row: 2 / span 1;
        max-inline-size: unset;
      }
    }
  }
}

.content-extra {
  grid-column: 1 / -1;
  display: grid;
}

.content-extra-text {
  overflow: hidden;
  block-size: 0px;
}

.content-extra-text>p+p {
  margin-block-start: 20px;
}

.content-btn {
  justify-self: end;
  display: flex;
  align-items: center;
  column-gap: 10px;
  color: var(--clr-accent);
  svg {
    inline-size: 30px;
    block-size: 30px;
    fill: var(--clr-accent);
    transition: transform var(--tr);
  }
  svg.rotate {
    transform: rotate(-180deg);
  }
}
