.footer {
  inline-size: 100%;
  z-index: 1;
  padding-block: 300px 90px;
  padding-block: 300px clamp(50px, calc(2.18rem + 4.71vw), 90px);
  padding-block: clamp(150px, calc(-1.34rem + 16.74vw), 300px) clamp(50px, calc(2.18rem + 4.71vw), 90px);
  background-image: url('../img/misc/footer-bg.webp'), url('../img/svg/misc/footer-bg.svg');
  background-repeat: no-repeat;
  background-position: center bottom, center top;
  background-position: center top, left top;
  background-size: 100% auto, 1920px auto;
  &__inner {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    align-items: end;
    column-gap: 30px;
    color: var(--clr-white);
  }
  &__address {
    grid-column: 1 / span 4;
    grid-row: 1 / span 1;
    inline-size: min(269px, 100%);
    margin-block-end: 50px;
    h4 {
      display: flex;
      align-items: center;
      gap: 10px;
      margin-block-end: 35px;
      svg {
        inline-size: 24px;
        block-size: 24px;
        fill: var(--clr-white);
      }
    }
    p {
      font-size: 14px;
    }
  }
  &__btns {
    justify-self: end;
    grid-column: 8 / span 5;
    grid-row: 3 / span 1;
    display: flex;
    flex-wrap: wrap;
    column-gap: 20px;
    row-gap: 10px;
    margin-block-start: 50px;
    .button {
      margin-inline: 0;
    }
  }
}

.footer.bg-2 {
  background-color: var(--clr-bg-2);
}

.socials {
  grid-column: 1 / span 4;
  grid-row: 2 / span 1;
  display: flex;
  gap: 20px;
  svg {
    inline-size: 30px;
    block-size: 30px;
    fill: var(--clr-white);
    transition: opacity var(--tr);
  }
  svg:hover {
    opacity: .7;
  }
}

.sec-menu {
  grid-column: 5 / span 3;
  grid-row: 1 / span 2;
  display: flex;
  flex-direction: column;
  gap: 30px;
  &__item {
    color: var(--clr-white);
    font-size: 14px;
    text-transform: uppercase;
    transition: opacity var(--tr);
  }
  &__item:hover {
    opacity: .7;
  }
}

.copyright,
.agreement-footer {
  color: var(--clr-white);
  font-size: 14px;
}

.copyright {
  grid-column: 1 / span 4;
  grid-row: 3 / span 1;
}

.agreement-footer {
  grid-column: 5 / span 3;
  grid-row: 3 / span 1;
  transition: opacity var(--tr);
}

.agreement-footer:hover {
  opacity: .7;
}

.page-home {
  .footer {
    background-color: #000;
  }
}

@media (max-width: 1200px) {
  .footer {
    &__address {
      grid-column: 1 / span 5;
    }
    &__btns {
      justify-self: start;
      grid-column: 6 / -1;
      grid-row: 3 / span 2;
      column-gap: 10px;
      margin-block-start: 0px;
    }
  }
  .socials {
    grid-column: 1 / span 5;
  }
  .sec-menu {
    grid-column: 6 / -1;
  }
  .copyright {
    grid-column: 1 / span 5;
    grid-row: 3 / span 1;
    margin-block-start: 50px;
  }
  .agreement-footer {
    grid-column: 1 / span 5;
    grid-row: 4 / span 1;
    margin-block-start: 10px;
  }
}

@media (max-width: 767px) {
  .footer {
    padding-block-start: 150px;
    padding-block-start: clamp(100px, calc(4.01rem + 11.19vw), 150px);
    background-position: center top -140px;
    background-position: center top, center top clamp(-230px, calc(-18.40rem + 20.13vw), -140px);
    &__inner {
      grid-template-columns: 1fr;
      align-items: start;
      column-gap: 0;
      row-gap: 0px;
    }
    &__address {
      grid-column: 1 / -1;
      margin-block-end: 40px;
      h4 {
        margin-block-end: 20px;
      }
    }
    &__btns {
      justify-self: center;
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
      justify-content: center;
      margin-block-end: 20px;
    }
  }
  .sec-menu {
    grid-column: 1 / -1;
    grid-row: 2 / span 1;
    gap: 20px;
    margin-block-end: 40px;
  }
  .socials {
    justify-self: center;
    grid-column: 1 / -1;
    grid-row: 4 / span 1;
    margin-block-end: 20px;
  }
  .copyright {
    justify-self: center;
    grid-column: 1 / -1;
    grid-row: 5 / span 1;
    margin-block-start: 0px;
  }
  .agreement-footer {
    justify-self: center;
    grid-column: 1 / -1;
    grid-row: 6 / span 1;
  }
}
