.lang {
  position: relative;
  display: flex;
  align-items: center;
  block-size: 100%;
  &__switch {
    display: flex;
    align-items: center;
    gap: 5px;
    color: var(--clr-text);
    font-size: 14px;
    line-height: 1.3;
    svg {
      inline-size: 16px;
      block-size: 16px;
      fill: var(--clr-accent);
      transition: transform var(--tr);
    }
  }
  &__submenu {
    visibility: hidden;
    position: absolute;
    inset-inline-start: 0;
    inset-block-start: 100%;
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 10px;
    padding: 15px 20px;
    background-color: var(--clr-white);
    font-size: 14px;
    line-height: 1.3;
    border: 1px solid var(--clr-border);
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.05);
    opacity: 0;
    transform: translateY(-10%);
    transition: transform var(--tr), opacity var(--tr), visibility 0s linear .3s;
  }
  &__submenu-item {
    color: var(--clr-text);
    transition: color var(--tr);
  }
  &__submenu-item:hover {
    color: var(--clr-accent);
  }
}

// .lang:hover,
.lang.active {
  .lang__switch {
    svg {
      transform: rotate(-180deg);
    }
  }
  .lang__submenu {
    visibility: visible;
    opacity: 1;
    transform: translateY(0%);
    transition: transform var(--tr), opacity var(--tr), visibility 0s linear 0s;
  }
}

@media (max-width: 1000px) {
  .lang__submenu {
    inset-inline-start: -15px;
  }
}
