.grid-search-results-wrap {
  z-index: 1;
  margin-block-end: 40px;
  padding-block: clamp(15px, 0.5846rem + 1.7647vw, 30px) clamp(15px, 0.7022rem + 1.1765vw, 25px);
  background-color: var(--clr-bg-light);
  border-block-end: 1px solid var(--clr-border);
}

.grid-search-results {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: clamp(0px, -0.7059rem + 3.5294vw, 30px);
  &__item {
    align-self: center;
    font-size: clamp(14px, 0.7338rem + 0.7059vw, 20px);
    line-height: 1.5;
    a {
      color: var(--clr-text);
      transition: color var(--tr);
    }
  }
  &__item:nth-child(1) {
    grid-column: 1 / span 3;
  }
  &__item:nth-child(2) {
    grid-column: 4 / span 3;
  }
  &__item:nth-child(3) {
    grid-column: 7 / -1;
  }
  &__img {
    --skew: 12%;
    overflow: hidden;
    position: relative;
    display: block;
    max-inline-size: 170px;
    clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, var(--skew) 100%);
  }
  &__img::before {
    content: '';
    position: absolute;
    z-index: 1;
    inset: 0;
    transform: translateX(-100%);
    background: hsl(351 80% 42% / .1);
    ;
    transition: transform var(--tr);
  }
}

.grid-search-results:hover {
  // cursor: pointer;
  a {
    color: var(--clr-accent);
    // transition: color var(--tr);
  }
  .grid-search-results__img::before {
    transform: translateX(0%);
  }
}

@media (max-width: 500px) {
  .grid-search-results.sr-menu {
    text-transform: uppercase;
    .grid-search-results__item:nth-child(1) {
      grid-column: 1 / span 2;
    }
    .grid-search-results__item:nth-child(2) {
      grid-column: 3 / span 3;
    }
    .grid-search-results__item:nth-child(3) {
      grid-column: 6 / -1;
    }
  }
}
