.catalog {
  overflow-x: auto;
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  column-gap: 30px;
  margin-block-end: 90px;
  &__item {
    flex-grow: 0;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    inline-size: clamp(214px, 7.3515rem + 30.1176vw, 470px);
    inline-size: clamp(214px, 9.7044rem + 18.3529vw, 370px);
  }
  &__item:hover {
    .catalog__title a {
      color: var(--clr-accent);
    }
  }
  &__item.first {
    flex-direction: row;
    align-items: center;
    padding-inline-start: 15px;
  }
  &__item.odd {
    .catalog__desc {
      order: 0;
      margin-inline-start: 0px;
      margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
    }
    .catalog__img {
      order: 1;
      margin-block-end: 0px;
    }
  }
  &__img {
    --h: 100%;
    --skew: calc(var(--h) / 8);
    aspect-ratio: 1.339 / 1;
    inline-size: 100%;
    margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
    background-image: linear-gradient(180deg, #F9F9F9 0%, #E6E6E6 100%);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, var(--skew) 100%);
  }
  &__desc {
    margin-inline-start: clamp(0px, -1.4118rem + 7.0588vw, 60px);
  }
  &__main-title {
    max-inline-size: 370px;
    margin-block-end: 0;
  }
  &__subtitle {
    margin-block-end: 10px;
    font-size: clamp(12px, 0.5618rem + 0.9412vw, 20px);
    line-height: 1.5;
  }
  &__title {
    margin-block-end: 10px;
    a {
      color: var(--clr-text);
      transition: color var(--tr);
    }
    a:visited {
      color: var(--clr-text);
    }
    a:hover {
      color: var(--clr-accent);
    }
  }
  &__list {
    margin: 0;
    padding-inline-start: 20px;
  }
}

@media (max-width: 768px) {
  .catalog {
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 30px;
    margin-inline: 15px;
    &__item {
      inline-size: 45%;
    }
    &__item.first {
      flex-direction: row;
      align-items: center;
      inline-size: 100%;
      padding-inline-start: 0;
    }
    &__item.odd {
      .catalog__desc {
        order: unset;
        margin-inline-start: 0;
        margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
      }
      .catalog__img {
        order: unset;
        margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
      }
    }
    &__desc {
      margin-inline-start: 0;
    }
  }
}

@media (max-width: 500px) {
  .catalog {
    &__item {
      inline-size: 100%;
    }
  }
}
