.prim-menu {
  display: flex;
  column-gap: 30px;
  column-gap: clamp(22px, -2.13rem + 5.48vw, 30px);
  block-size: 100%;
  font-size: 14px;
  line-height: 1;
  &__item {
    display: flex;
    color: var(--clr-text);
  }
  &__item.about {
    position: relative;
  }
  &__btn.active {
    color: var(--clr-accent);
  }
  &__btn.btn-about {
    svg {
      transition: transform var(--tr);
    }
  }
  &__btn.btn-about.active {
    svg {
      transform: rotate(-180deg);
    }
  }
  &__link {
    align-self: center;
    text-transform: uppercase;
    transition: color var(--tr);
  }
  &__btn {
    display: flex;
    align-items: center;
    gap: 5px;
    color: var(--clr-text);
    text-transform: uppercase;
    transition: color var(--tr);
    svg {
      inline-size: 16px;
      block-size: 16px;
      fill: var(--clr-accent);
    }
  }
  &__link:hover,
  &__btn:hover {
    color: var(--clr-accent);
  }
  &__submenu {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    z-index: -1;
    inset-inline-start: 0%;
    inset-block-start: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 40px;
    inline-size: 100%;
    padding-block: 45px;
    transform: translateY(-20px);
    transition: transform var(--tr), opacity var(--tr), visibility 0s linear .3s;
  }
  &__submenu.active {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
    transition: transform var(--tr), opacity var(--tr), visibility 0s linear 0s;
  }
  &__submenu::before {
    --mw: 100%;
    content: '';
    display: block;
    position: absolute;
    z-index: -1;
    inset: 0 -100%;
    inset: 0;
    background-color: var(--clr-white);
    box-shadow: 0 10px 10px -10px hsl(0 0% 0% / .1);
  }
  &__submenu.active::before {
    inset: 0 -100%;
  }
  &__customer:first-child {
    padding-inline-end: 30px;
    padding-inline-end: 0px;
    border-inline-end: 1px solid var(--clr-bg-gray);
  }
  &__title {
    display: flex;
    align-items: center;
    column-gap: 5px;
    margin-block-end: 20px;
    a {
      color: var(--clr-text);
      transition: color var(--tr);
    }
    a:hover {
      color: var(--clr-accent);
    }
    svg {
      display: none;
      inline-size: 16px;
      block-size: 16px;
      fill: var(--clr-accent);
    }
  }
  &__goods {
    --min-w: 250px;
    --max-w: 1fr;
    overflow-y: auto;
    overflow-x: hidden;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(min(var(--min-w), 100%), var(--max-w)));
    row-gap: 10px;
  }
  .goods-item {
    display: flex;
    align-items: center;
    gap: 10px;
    color: var(--clr-text);
    transition: color var(--tr);
  }
  .goods-item:hover {
    color: var(--clr-accent);
  }
  .goods-img {
    inline-size: 40px;
    block-size: 40px;
    background-color: #D9D9D9;
  }
  .submenu-about {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    z-index: -1;
    inset-inline-start: 0%;
    inset-block-start: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 30px;
    background-color: var(--clr-white);
    border: 1px solid var(--clr-bg-gray);
    transform: translateY(-20px);
    transition: transform var(--tr), opacity var(--tr), visibility 0s linear .3s;
    &__item:link,
    &__item:visited {
      inline-size: max-content;
      color: var(--clr-gray);
      font-size: 14px;
      transition: color var(--tr);
    }
    &__item:hover {
      color: var(--clr-accent);
    }
  }
  .submenu-about.active {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
    transition: transform var(--tr), opacity var(--tr), visibility 0s linear 0s;
  }
}

@media (max-width: 1000px) {
  .prim-menu {
    overflow-y: auto;
    overflow-x: hidden;
    position: absolute;
    inset-inline-start: 0;
    inset-block-start: 113px;
    flex-direction: column;
    row-gap: 25px;
    row-gap: 0px;
    inline-size: 100%;
    block-size: calc(100vh - 248px);
    padding: 0 15px;
    background-color: var(--clr-white);
    transform: translateX(-100%);
    transition: transform var(--tr);
    &__item {
      display: unset;
      .btn-products {
        display: none;
      }
      .btn-about {
        margin-block-end: 25px;
      }
    }
    &__item:nth-child(1) {
      order: 1;
    }
    &__item:nth-child(2) {
      order: 0;
    }
    &__item:nth-child(3) {
      order: 2;
    }
    &__item:nth-child(4) {
      order: 3;
    }
    &__item:nth-child(5) {
      order: 4;
    }
    &__customer:first-child {
      border-inline-end: none;
    }
    &__submenu {
      visibility: visible;
      opacity: 1;
      position: static;
      z-index: 1;
      inset-inline-start: 0%;
      inset-block-start: 100%;
      display: block;
      grid-template-columns: 1fr 1fr;
      column-gap: 40px;
      inline-size: 100%;
      padding-block: unset;
      background-color: unset;
      transform: unset;
      transition: unset;
    }
    .submenu-about {
      overflow: hidden;
      visibility: unset;
      opacity: unset;
      position: static;
      z-index: unset;
      inset-inline-start: unset;
      inset-block-start: unset;
      display: flex;
      flex-direction: column;
      gap: 15px;
      max-block-size: 0px;
      padding: unset;
      padding-inline-start: 15px;
      background-color: unset;
      border: unset;
      transform: unset;
      transition: unset;
      transition: max-block-size var(--tr), margin-top var(--tr), padding var(--tr);
      &__item {
        inline-size: max-content;
        color: var(--clr-gray);
        font-size: 14px;
        transition: color var(--tr);
      }
      &__item:hover {
        color: var(--clr-accent);
      }
    }
    .submenu-about.active {
      max-block-size: 250px;
      margin-block-start: -10px;
      padding: 0 0 15px 15px;
      transition: max-block-size var(--tr), margin-top var(--tr), padding var(--tr);
    }
    &__submenu::before {
      content: none;
    }
    &__title {
      margin-block-end: 25px;
      font-size: 14px;
      font-weight: var(--fw-r);
      text-transform: uppercase;
      cursor: pointer;
      transition: color var(--tr);
      svg {
        display: block;
        transition: transform var(--tr);
      }
    }
    &__title.active {
      color: var(--clr-accent);
      svg {
        transform: rotate(-180deg);
      }
    }
    &__goods {
      display: block;
      max-block-size: 0;
      margin-block-end: 0px;
      transition: max-block-size var(--tr), margin-block-start var(--tr), padding-block-end var(--tr);
    }
    &__goods.active {
      max-block-size: 500px;
      margin-block-start: -15px;
      padding-block-end: 15px;
    }
    &__link {
      align-self: unset;
      margin-block-end: 25px;
    }
  }
  .prim-menu.active {
    transform: translateX(0);
  }
  .prim-menu::-webkit-scrollbar {
    width: 5px;
  }
  .prim-menu::-webkit-scrollbar-track {
    background-color: var(--clr-bg-gray);
    border-radius: 100vw;
  }
  .prim-menu::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent);
    border-radius: 100vw;
  }
}
