.prof-categ {
  --skew: 40px;
  --skew2: 15px;
  --skew-b-a: 8.5deg;
  --tr: .4s linear;
  --tt: 20px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
  row-gap: 40px;
  margin: 0 0 clamp(60px, 2.1029rem + 8.2353vw, 130px);
  padding: 0;
  list-style: none;
  &__item {
    //  outline: 1px solid;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    inline-size: 100%;
    // max-block-size: 330px;
  }
  &__item::before {
    content: '';
    position: absolute;
    z-index: 1;
    inset-inline-start: 15%;
    inset-block-start: 100%;
    transform: translate(0px, -100%) skewX(var(--skew-b-a));
    display: block;
    inline-size: 3px;
    block-size: 98px;
    background-color: var(--clr-accent);
    transition: transform var(--tr), inset-block-start var(--tr);
  }
  &__item::after {
    content: '';
    position: absolute;
    z-index: 1;
    inset-inline-end: 20px;
    inset-block-start: 0;
    transform: skewX(var(--skew-b-a));
    display: block;
    inline-size: 3px;
    block-size: 98px;
    background-color: var(--clr-accent);
    transition: transform var(--tr), inset-block-start var(--tr);
  }
  &__img {
    overflow: hidden;
    position: relative;
    inline-size: 100%;
    img {
      clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, var(--skew) 100%);
    }
  }
  // &__img::before {
  //   content: '';
  //   position: absolute;
  //   z-index: 1;
  //   inset: 0;
  //   transform: translateX(100%);
  //   background: linear-gradient(100deg, rgba(255, 255, 255, 0.00) 34.54%, rgba(255, 255, 255, 0.73) 57%, rgba(255, 255, 255, 0.00) 75.23%);
  //   transition: transform var(--tr);
  // }
  &__title {
    // border: 1px solid;
    position: relative;
    z-index: 2;
    display: flex;
    align-items: end;
    min-block-size: 98px;
    margin-inline-end: 0px;
    margin-block-start: -40px;
    padding: 20px 20px 20px 75px;
    color: var(--clr-text);
    background: transparent;
    clip-path: polygon(0 0, calc(100% - var(--skew2)) 0, 100% 100%, var(--skew2) 100%);
    transition: color var(--tr), background-color var(--tr);
    span {
      transform: translateY(20px);
      transition: transform var(--tr);
    }
  }
}

.prof-categ__item:hover {
  .prof-categ__img::before {
    transform: translateX(0%);
  }
  .prof-categ__title {
    margin-inline-end: 15px;
    padding: 20px 20px 20px 40px;
    color: var(--clr-white);
    background: rgba(64, 83, 93, 0.80);
    span {
      transform: translateY(0px);
    }
  }
}

.prof-categ__item:hover::before {
  inset-inline-start: -1.5%;
  inset-block-start: 0%;
  transform: translate(0px, 0%) skewX(var(--skew-b-a));
}

.prof-categ__item:hover::after {
  inset-inline-end: 10px;
  inset-block-start: 100%;
  transform: translate(0px, -100%) skewX(var(--skew-b-a));
}

.prof-categ.auto-grid {
  --min-w: 280px;
  --r-g: 40px;
}

