.tabs-acc {
  --tr: .6s ease-in-out;
  overflow: hidden;
  position: relative;
  &__tabs {
    display: flex;
    column-gap: 60px;
    column-gap: clamp(15px, -0.12rem + 5.29vw, 60px);
    border-block-end: 1px solid var(--clr-border);
  }
  &__tab {
    padding-block-end: 15px;
    color: var(--clr-text);
    font-size: 14px;
    text-transform: uppercase;
    border-block-end: 1px solid transparent;
    transform: translateY(1px);
  }
  &__tab.active {
    color: var(--clr-text);
    font-weight: var(--fw-m);
    border-block-end: 1px solid var(--clr-red);
  }
  &__content {
    padding: clamp(30px, 1.64rem + 1.18vw, 40px) 0;
    transition: transform var(--tr), opacity var(--tr);
  }
  &__content.hidden {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    transform: translateX(-10%);
  }
  &__content-inner {
    padding-inline-end: 15px;
    h4 {
      margin-block-end: clamp(20px, 1.01rem + 1.18vw, 30px);
    }
    ul {
      margin-block: 0;
      padding-inline-start: 30px;
      padding-block: 0;
      ul {
        margin-block-start: 15px;
      }
    }
    ul>li+li {
      margin-block-start: 20px;
    }
  }
}

.tabs-acc--filter {
  overflow: visible;
  padding-inline: 0 !important;
  .tabs-acc__tabs {
    padding-block-start: 0;
    .arrow {
      display: none;
    }
  }
  .tabs-acc__tab {
    display: flex;
    align-items: center;
    gap: 5px;
    padding-block-end: 10px;
    transform: translateY(1px);
    color: var(--clr-text);
    svg {
      inline-size: 30px;
      block-size: 30px;
      fill: var(--clr-light-text);
    }
  }
  .tabs-acc__tab.active {
    svg {
      fill: var(--clr-accent);
    }
  }
  .tabs-acc__content {
    padding-block: 30px 0;
    transition: unset;
  }
  .tabs-acc__content-inner {
    padding-inline-end: 0 !important;
  }
}

@media (max-width: 1000px) {
  .tabs-acc {
    margin-inline: -15px;
    padding-inline: 15px;
    &__tabs {
      margin-inline: -15px;
      padding-inline-start: 15px;
      padding-block-start: 15px;
      background-color: var(--clr-bg-gray);
      // border-block-end: none;
    }
    &__tabs.fixed {
      position: fixed;
      z-index: 200;
      top: 40px;
      inline-size: 100%;
      margin-inline-start: -15px;
      padding-inline-start: 15px;
    }
    &__tab {
      transform: unset;
    }
    &__content.fixed {
      padding-block-start: 93px;
    }
  }
  .tabs-acc--filter {
    .tabs-acc__tabs {
      margin-inline: 0 !important;
      padding-inline: 0 !important;
    }
  }
  .page-home {
    .tabs-acc {
      .tabs-acc__tabs {
        background-color: var(--clr-white);
      }
    }
  }
}

@media (max-width: 550px) {
  .tabs-acc--filter {
    .tabs-acc__tabs {
      display: grid;
      grid-template-columns: 1fr min-content;
      grid-template-rows: min-content min-content;
      border-block-end: none;
      .arrow {
        // display: block;
        inline-size: 18px;
        block-size: 18px;
        fill: var(--clr-accent);
        transform: translateY(50%) rotate(90deg);
        transition: transform .3s ease-in-out;
      }
      .arrow.active {
        transform: translateY(50%) rotate(-90deg);
      }
    }
    .tabs-acc__tabs :nth-child(1) {
      grid-column: 1 / span 1;
      grid-row: 1 / span 1;
    }
    .tabs-acc__tabs :nth-child(2) {
      grid-column: 1 / span 1;
      grid-row: 2 / span 1;
    }
    .tabs-acc__tabs :nth-last-child(1) {
      grid-column: 2 / span 1;
      grid-row: 1 / span 2;
    }
    .tabs-acc__tab {
      padding-block-end: 0;
      border-block-end: none;
    }
    .tabs-acc__tab:not(.active) {
      overflow: hidden;
      // max-block-size: 0;
    }
    .tabs-acc__tab.open {
      // max-block-size: 100px;
    }
    .tabs-acc__content {
      padding-block-start: 10px;
    }
  }
}

@media (max-width: 390px) {
  .tabs-acc {
    &__tabs {
      overflow-y: hidden;
      overflow-x: scroll;
      position: relative;
      padding-inline-end: 15px;
    }
    &__tabs::-webkit-scrollbar {
      display: none;
    }
    &__tabs>* {
      flex-grow: 0;
      flex-shrink: 0;
      position: relative;
    }
  }
}
