.modal {
  position: fixed;
  // z-index: 10000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
  &__content {
    background: #fff;
    padding: 20px;
  }
}

.modal.open {
  display: flex;
}

