.art-potreb-func {
  display: grid;
  margin-block-end: clamp(70px, 2.9632rem + 7.0588vw, 130px);
  &__img {
    justify-self: center;
    max-inline-size: 969px;
    margin-block-end: clamp(0px, 3.4412rem + -4.7059vw, 40px);
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
  }
  &__desc {
    grid-column: 1 / span 6;
    max-inline-size: 467px;
  }
  &__title {
    margin-block-end: clamp(10px, 0.3897rem + 1.1765vw, 20px);
  }
  &__text {}
  &__list {
    grid-column: 7 / span 6;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin: 60px 0 0 0;
    padding: 0;
    list-style: none;
  }
  &__item {
    display: grid;
    row-gap: clamp(5px, 0.0772rem + 1.1765vw, 15px);
    .h3 {
      font-size: clamp(18px, 0.8426rem + 1.4118vw, 30px);
    }
    p {
      font-size: clamp(14px, 0.7809rem + 0.4706vw, 18px);
    }
  }
}

@media (max-width: 767px) {
  .art-potreb-func {
    &__img {
      justify-self: unset;
      max-inline-size: 570px;
    }
    &__content {
      grid-template-columns: 1fr;
      column-gap: unset;
      row-gap: 30px;
    }
    &__desc {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      max-inline-size: unset;
    }
    &__list {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      margin-block-start: 0;
    }
  }
}

@media (max-width: 390px) {
  .art-potreb-func {
    &__list {
      grid-template-columns: 1fr;
      column-gap: unset;
      row-gap: 30px;
    }
  }
}
