.category-wrap {
  inline-size: 100vw;
  padding-block: clamp(30px, 0.6985rem + 5.8824vw, 80px);
}

.category-wrap.odd {
  position: relative;
  background-color: var(--clr-bg-2);
}

.category-top-block {
  display: block;
  aspect-ratio: 8.3842 / 1;
  inline-size: 100vw;
  background-image: url(../img/svg/misc/category-triangle-top.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

.category-bottom-block {
  display: block;
  aspect-ratio: 8.3842 / 1;
  inline-size: 100vw;
  background-image: url(../img/svg/misc/category-triangle-bottom.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

.category {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
  margin-block-end: 100px;
  &__content {
    grid-column: 1 / span 6;
  }
  &__title {
    margin-block-end: 20px;
  }
  &__desc {
    margin-block-end: 30px;
  }
  &__desc>p+p {
    margin-block-start: 20px;
  }
  &__imgs {
    position: relative;
    justify-self: end;
    grid-column: 7 / span 6;
    display: grid;
    aspect-ratio: 0.969 / 1;
    max-inline-size: 470px;
  }
  &__img {
    --h: 58.7628%;
    --skew: calc(var(--h) / 6);
    // aspect-ratio: 1.2175 / 1;
    inline-size: 73.8297%;
    block-size: var(--h);
    clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, var(--skew) 100%);
  }
  &__img.i-top {
    grid-column: 1;
    grid-row: 1;
    justify-self: end;
  }
  &__img.i-btm {
    grid-column: 1;
    grid-row: 1;
    align-self: end;
  }
  &__img-before {
    position: absolute;
    z-index: 1;
    inset-inline-start: 0;
    inset-block-start: 0;
    display: block;
    inline-size: 1.4893%;
    block-size: 32.9896%;
    background-color: var(--clr-accent);
    transform: translate(400%, 75%) skew(10deg);
    opacity: 1;
  }
  &__img-after {
    position: absolute;
    z-index: 1;
    inset-inline-end: 0;
    inset-block-end: 0;
    display: block;
    inline-size: 1.9148%;
    block-size: 37%;
    background-color: var(--clr-gray);
    transform: translate(-1100%, -11.25%) skew(7deg);
    opacity: 1;
  }
}

@media (max-width: 1000px) {
  .category {
    &__imgs {
      max-inline-size: unset;
      inline-size: 100%;
    }
  }
}

@media (max-width: 768px) {
  .category {
    grid-template-columns: 1fr;
    column-gap: 0;
    row-gap: 20px;
    &__imgs {
      justify-self: center;
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      max-inline-size: 470px;
      inline-size: unset;
    }
    &__content {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
  }
}
