.accordion-auto {
  .ac-trigger {
    display: flex;
    justify-content: space-between;
    margin-block-end: 10px;
    padding: clamp(15px, 0.8199rem + 0.5882vw, 20px) clamp(15px, 0.5846rem + 1.7647vw, 30px);
    background-color: var(--clr-bg-light);
    font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
    border: 1px solid var(--clr-border);
    transition: background-color var(--tr);
    svg {
      inline-size: clamp(20px, 1.0147rem + 1.1765vw, 30px);
      block-size: clamp(20px, 1.0147rem + 1.1765vw, 30px);
      fill: var(--clr-accent);
      transition: transform var(--tr);
    }
  }
  .ac-trigger[aria-expanded='true'] {
    background-color: var(--clr-bg-2);
    svg {
      transform: rotate(-180deg);
    }
  }
  table {
    min-inline-size: 540px;
    padding: clamp(15px, 0.5846rem + 1.7647vw, 30px) clamp(15px, 0.5846rem + 1.7647vw, 30px) clamp(15px, 0.8199rem + 0.5882vw, 20px);
    background-color: var(--clr-bg-light);
    border-inline-end: 1px solid var(--clr-border);
    border-block-end: 1px solid var(--clr-border);
    border-inline-start: 1px solid var(--clr-border);
    border-spacing: 0;
  }
  thead th {
    border-block-end: 1px solid var(--clr-border);
  }
  th {
    inline-size: clamp(60px, 2.3382rem + 7.0588vw, 120px);
    padding-block-end: 20px;
    color: var(--clr-light-text);
    font-size: clamp(12px, 0.7029rem + 0.2353vw, 14px);
    font-weight: var(--fw-r);
    text-align: center;
  }
  th:nth-child(1) {
    inline-size: unset;
    text-align: left;
  }
  td {
    padding: clamp(10px, 0.3897rem + 1.1765vw, 20px) 0;
    font-size: clamp(12px, 0.7029rem + 0.2353vw, 14px);
    text-align: center;
  }
  td:nth-child(1) {
    font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
    text-align: left;
  }
}

.accordion-auto-inner {
  margin-inline-start: 30px;
  .ac-panel {
    margin-block: -10px 10px;
  }
}

@media (max-width: 768px) {
  .accordion-auto-inner {
    margin-inline-start: 15px;
  }
}
