.art-potreb-elem {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;
  column-gap: 30px;
  margin-block-end: clamp(80px, 3.8235rem + 5.8824vw, 130px);
  &__img {
    grid-column: 1 / span 8;
  }
  &__desc {
    grid-column: 9 / span 4;
  }
  &__text>p+p {
    margin-block-start: 20px;
  }
}

@media (max-width: 767px) {
  .art-potreb-elem {
    grid-template-columns: 1fr;
    align-items: unset;
    column-gap: 0;
    row-gap: 20px;
    &__img {
      grid-column: 1 / -1;
    }
    &__desc {
      grid-column: 1 / -1;
    }
  }
}
