.about-company {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 30px;
  row-gap: 20px;
  // margin-block-end: 150px;
  padding-block-end: clamp(60px, 1.6324rem + 10.5882vw, 150px);
  &__title-block {
    grid-column: 1 / span 6;
    grid-row: 1 / span 1;
  }
  &__title {}
  &__title-intro {}
  &__imgs {
    justify-self: end;
    grid-column: 7 / span 6;
    grid-row: 1 / span 1;
    margin-block: -30px 0;
  }
  &__desc {
    grid-column: 1 / -1;
    grid-row: 2 / span 1;
  }
  &__desc>p+p {
    margin-block-start: 20px;
  }
}

@media (max-width: 850px) {
  .about-company {
    &__imgs {
      margin-block-start: 0;
    }
  }
}

@media (max-width: 768px) {
  .about-company {
    grid-template-columns: 1fr;
    column-gap: unset;
    &__imgs {
      justify-self: center;
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      margin-block-start: 0;
    }
    &__title-block {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
    &__desc {
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
    }
  }
}
