.art-potreb-sravn {
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: clamp(15px, 0.5846rem + 1.7647vw, 30px);
  min-inline-size: 610px;
  margin-block-end: 60px;
  will-change: transform;
  &__prod {
    position: relative;
    display: grid;
    .pin-spacer {
      z-index: 1 !important;
    }
  }
  &__prod.one {
    grid-column: 1 / span 6;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 30px;
    .art-potreb-sravn__top {
      // justify-self: end;
      grid-column: 4 / span 3;
    }
    .art-potreb-sravn__table {
      grid-column: 1 / -1;
    }
    .art-potreb-sravn__table-item {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      column-gap: 30px;
       :first-child {
        grid-column: 1 / span 3;
      }
       :last-child {
        grid-column: 4 / span 3;
      }
    }
    .button {
      grid-column: 4 / span 3;
    }
  }
  &__prod.two {
    grid-column: 7 / span 3;
  }
  &__prod.three {
    grid-column: 10 / span 3;
  }
  &__top {
    max-inline-size: 270px;
    inline-size: 100%;
    margin-block-end: 30px;
    background-color: var(--clr-white);
  }
  &__prod.active {
    .art-potreb-sravn__top {
      position: absolute;
      z-index: 1;
      inset-inline-end: 0;
      inset-block-start: 0;
    }
  }
  &__img {
    overflow: hidden;
    aspect-ratio: 2.25 / 1;
    inline-size: 100%;
    // block-size: 120px;
    border-inline: 1px solid var(--clr-border);
    border-block-start: 1px solid var(--clr-border);
    img {
      block-size: 100%;
      object-fit: contain;
    }
  }
  &__list {
    position: relative;
  }
  &__cur {
    display: flex;
    justify-content: space-between;
    align-items: center;
    block-size: clamp(66px, 3.8897rem + 1.1765vw, 76px);
    padding-inline: clamp(10px, 0.3897rem + 1.1765vw, 20px);
    background-color: var(--clr-bg-light);
    font-size: clamp(16px, 0.8588rem + 0.7059vw, 22px);
    font-weight: var(--fw-m);
    line-height: 1.3;
    border: 1px solid var(--clr-border);
    cursor: pointer;
    transition: background-color var(--tr);
    svg {
      min-inline-size: 20px;
      inline-size: 20px;
      block-size: 20px;
      fill: var(--clr-accent);
      transform: rotate(90deg);
      transition: transform var(--tr);
    }
  }
  &__cur.active {
    background-color: var(--clr-bg-gray);
    svg {
      transform: rotate(-90deg);
    }
  }
  &__select {
    overflow: hidden;
    position: absolute;
    z-index: 0;
    inset-inline-start: 0;
    inset-block-start: clamp(66px, 3.8897rem + 1.1765vw, 76px);
    inline-size: 100%;
    max-block-size: 0;
    background-color: var(--clr-white);
    transition: max-block-size var(--tr);
  }
  &__select.active {
    max-block-size: 400px;
  }
  &__select-inner {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin: 0;
    padding: 20px;
    border-inline: 1px solid var(--clr-border);
    border-block-end: 1px solid var(--clr-border);
  }
  &__option {
    display: flex;
    align-items: center;
    gap: 5px;
    cursor: pointer;
    .opt-img {
      display: flex;
      align-items: center;
      inline-size: 40px;
      block-size: 40px;
      img {}
    }
    span {
      font-size: 16px;
      font-weight: var(--fw-m);
    }
  }
  &__table:not(:last-child) {
    margin-block-end: clamp(40px, 2.2647rem + 1.1765vw, 50px);
  }
  &__table-title {
    margin-block-end: clamp(20px, 1.1559rem + 0.4706vw, 24px);
  }
  &__table-item {
    padding-block: clamp(10px, 0.3897rem + 1.1765vw, 20px);
    font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
     :first-child {
      font-weight: var(--fw-b);
    }
     :last-child {
      display: block;
      text-align: center;
    }
  }
  &__prod.two,
  &__prod.three {
    .art-potreb-sravn__table-title {
      color: transparent;
    }
    .art-potreb-sravn__table-item span:first-child {
      display: none;
    }
  }
  .button {
    position: relative;
    z-index: 0;
    justify-self: center;
    max-inline-size: 153px;
  }
}

@media (max-width: 992px) {
  .art-potreb-sravn {
    &__prod {
      display: block;
    }
    &__prod.one {
      grid-column: 1 / span 4;
      display: block;
      .art-potreb-sravn__table-item {
        display: block;
      }
    }
    &__prod.two {
      grid-column: 5 / span 4;
    }
    &__prod.three {
      grid-column: 9 / span 4;
    }
    &__top {
      max-inline-size: unset;
    }
    &__prod.two,
    &__prod.three {
      .art-potreb-sravn__table-title {
        color: transparent;
      }
      .art-potreb-sravn__table-item span:first-child {
        display: block;
        color: transparent;
      }
    }
    &__table-item {
       :last-child {
        display: block;
        text-align: left;
      }
    }
  }
}

@media (max-width: 768px) {
  .art-potreb-sravn {
    &__select-inner {
      gap: 10px;
      padding: 10px;
    }
    &__option {
      flex-direction: column;
      align-items: center;
      column-gap: unset;
      row-gap: 5px;
      .opt-img {
        inline-size: 30px;
        block-size: 30px;
      }
      span {
        font-size: 14px;
        font-weight: var(--fw-r);
      }
    }
  }
}
