.slider-home {
  --h: 950px;
  overflow: hidden;
  position: relative;
  z-index: 0;
  block-size: var(--h);
  color: white;
  background: #2A404C;
  &__imgs {
    overflow: hidden;
    position: absolute;
    z-index: 0;
    inset-inline-start: 0;
    inset-block-end: 0;
    inline-size: 70%;
  }
  &__img {
    inline-size: auto;
    block-size: var(--h);
    object-fit: unset;
  }
  .container {
    position: relative;
    z-index: 1;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 0 30px;
    block-size: 100%;
  }
  .container::before {
    content: '';
    position: absolute;
    z-index: -1;
    inset: 0;
    background-image: url('../img/svg/misc/slider-home-skew-bg.svg');
    background-repeat: no-repeat;
    background-position: clamp(230px, -16.3099rem + 47.9452vw, 300px) 0;
  }
  .container::after {
    content: '';
    position: absolute;
    z-index: 1;
    inset-inline-start: clamp(180px, -15.0187rem + 54.7264vw, 400px);
    inset-block-start: 0;
    aspect-ratio: .5297 / 1;
    inline-size: clamp(41px, 1.4331rem + 5.6471vw, 89px);
    background-image: url('../img/svg/misc/slider-home-stripes.svg');
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }
  &__slides {
    grid-column: 4 / -1;
    position: relative;
    inset-inline-start: 0;
    inset-block-start: 0;
    block-size: 100%;
  }
  &__slide {
    block-size: var(--h);
    padding-inline-start: clamp(100px, -12.5rem + 39.0625vw, 200px);
    padding-block-start: clamp(160px, -3.125rem + 27.3438vw, 230px);
    opacity: 0;
    transition: opacity 1.5s ease-in-out;
    .logo-slider {
      aspect-ratio: 5.0892 / 1;
      inline-size: clamp(103px, 2.1551rem + 21.4118vw, 285px);
    }
  }
  &__slide.active {
    opacity: 1;
  }
  &__heading {
    margin-block-end: 20px;
    font-family: var(--ff-sec);
    font-size: clamp(14px, 0.3809rem + 2.4706vw, 35px);
    font-weight: var(--fw-b);
    text-transform: uppercase;
  }
  &__text {
    margin-block-end: 30px;
    margin-inline-start: 30px;
  }
  .button {
    margin-inline-start: clamp(20px, 0.3088rem + 4.7059vw, 60px);
  }
  &__pagination {
    position: absolute;
    z-index: 2;
    inset-inline-end: clamp(390px, -23.8442rem + 75.3425vw, 500px);
    inset-block-end: 350px;
    display: flex;
    gap: 0 10px;
  }
  &__pagination-item {
    inline-size: clamp(5px, 0.2419rem + 0.3529vw, 8px);
    block-size: clamp(5px, 0.2419rem + 0.3529vw, 8px);
    background-color: var(--clr-white);
    transition: inline-size var(--tr);
    // cursor: pointer;
  }
  &__pagination-item.active {
    inline-size: 30px;
    background-color: var(--clr-red);
  }
}

@media (max-width: 1024px) {
  .slider-home {
    --h: clamp(312px, 8.4773rem + 55.1136vw, 700px);
    .container::before {
      background-position: clamp(120px, -13.125rem + 42.9688vw, 230px) 0;
    }
    &__pagination {
      inset-inline-end: 50px;
      inset-block-end: 100px;
    }
  }
}

@media (max-width: 768px) {
  .slider-home {
    .container {
      gap: 0;
    }
    .container::before {
      background-position: clamp(70px, 2.1429rem + 11.1607vw, 120px) 0;
    }
    .container::after {
      inset-inline-start: clamp(86px, 1.1786rem + 20.9821vw, 180px);
    }
    &__slide {
      padding-inline-start: clamp(50px, 0.8929rem + 11.1607vw, 100px);
      padding-block-start: clamp(50px, -1.7857rem + 24.5536vw, 160px);
    }
    &__text {
      display: none;
    }
    &__pagination {
      inset-inline-end: 50px;
      inset-block-end: 15%;
    }
  }
}

@media (max-width: 320px) {
  .slider-home {
    block-size: 312px;
    &__pagination {
      inset-inline-end: 15px;
      inset-block-end: 50px;
    }
  }
}
