.art-prof-search-results {
  display: flex;
  gap: 0 10px;
  margin-block-end: 15px;
  color: var(--clr-text);
  font-size: 16px;
  transition: color var(--tr);
  svg {
    inline-size: 24px;
    block-size: 24px;
    fill: var(--clr-accent);
    transition: transform var(--tr);
  }
}

.art-prof-search-results:hover {
  color: var(--clr-accent);
  svg {
    transform: translateX(-50%);
  }
}

.art-prof-artikul {
  margin-block-end: clamp(20px, 0.8971rem + 1.7647vw, 35px);
  color: var(--clr-light-text);
}

.art-prof-intro {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
  margin-block-end: clamp(60px, 3.2794rem + 2.3529vw, 80px);
  // &__img {
  //   grid-column: 1 / span 4;
  //   border: 1px solid black;
  // }
  &__sliders {
    grid-column: 1 / span 4;
    @media (max-width: 768px) {
      grid-column: 1 / span 12;
      max-inline-size: 500px;
    }
    .splide__slide{
      overflow: hidden;
      aspect-ratio: 1.423/1;
    }
    .splide__arrow {
      background: unset;
    }
    .splide__arrow--prev {
      left: 0em;
    }
    .splide__arrow--next {
      right: 0em;
    }
    .splide__arrow svg {
      fill: var(--clr-accent);
    }
  }
  &__slider-main{}
  &__slider-sec{
    .splide__slide{
      border: 1px solid var(--clr-border);
    }
    .splide__slide.is-active{
      border: 1px solid  var(--clr-accent);
    }

  }
  &__desc {
    grid-column: 5 / -1;
    // border: 1px solid black;
  }
  &__text {
    margin-block-end: 40px;
  }
  &__text>p+p {
    margin-block-start: 20px;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 30px;
    row-gap: 20px;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 10px;
    color: var(--clr-accent);
    svg {
      inline-size: 40px;
      block-size: 40px;
      fill: var(--clr-accent);
    }
  }
  &__item:hover {
    text-decoration: underline;
  }
}

@media (max-width: 768px) {
  .art-prof-intro {
    column-gap: unset;
    row-gap: 20px;
    &__img {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      max-inline-size: 470px;
    }
    &__desc {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
  }
}

.art-prof-acc {
  margin-block-end: 60px;
  .tabs-acc__tabs {
    padding-inline-end: 15px;
  }
  .arrow {
    display: none;
    justify-self: end;
    inline-size: 20px;
    block-size: 20px;
    fill: var(--clr-accent);
  }
  .tabs-acc__tab {
    display: flex;
    align-items: center;
    column-gap: 5px;
    font-size: clamp(13px, 0.789rem + 0.1176vw, 14px);
    svg {
      inline-size: 30px;
      block-size: 30px;
      fill: var(--clr-light-text);
    }
  }
  .tabs-acc__tab.active {
    svg {
      fill: var(--clr-accent);
    }
  }
  .tabs-acc__content {
    padding-block-end: 0;
  }
  .tabs-acc__content-inner {
    padding-inline-end: 0;
  }
}

@media (max-width: 1000px) {
  .art-prof-acc {
    .tabs-acc__tabs {
      display: grid;
      grid-template-columns: 1fr min-content;
      gap: 0;
      padding-block-end: 15px;
    }
    .tabs-acc__tabs.fixed {
      position: fixed;
      inset-block-start: 100px;
    }
    .tabs-acc__tab.active {
      border-block-end: none;
    }
    .tabs-acc__tab:nth-child(1) {
      grid-column: 1 / span 1;
      grid-row: 1 / span 1;
    }
    .tabs-acc__tab:nth-child(2) {
      grid-column: 1 / span 1;
      grid-row: 2 / span 1;
    }
    .tabs-acc__tab:nth-child(3) {
      grid-column: 1 / span 1;
      grid-row: 3 / span 1;
    }
    .tabs-acc__tab {
      padding-block-end: 0;
    }
    .tabs-acc__tab:not(.active) {
      display: none;
    }
    .tabs-acc__tab.open {
      display: flex;
      margin-block-start: 10px;
    }
    .tabs-acc__tab:nth-child(1).open {
      margin-block-start: 0;
    }
    .arrow {
      display: block;
      grid-column: 2 / span 1;
      grid-row: 1 / span 3;
      position: relative;
      inset-block-start: 5px;
      transition: transform var(--tr);
    }
    .arrow.rotate {
      transform: rotate(180deg);
    }
  }
}

.art-prof-acc {
  .chars {
    &__title {}
    &__list {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: min-content;
      align-items: start;
      gap: 0 30px;
      margin: 0;
      padding: 0;
      list-style: none;
    }
    &__item {
      display: flex;
      justify-content: space-between;
      margin-block-start: 0;
      padding: clamp(15px, 0.8199rem + 0.5882vw, 20px) clamp(15px, 0.5846rem + 1.7647vw, 30px);
      font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
    }
    &__item:nth-child(4n+1) {
      background-color: var(--clr-bg-gray);
    }
    &__item:nth-child(4n+2) {
      background-color: var(--clr-bg-gray);
    }
    &__item {
      span {
        flex-basis: 50%;
      }
    }
    &__heading {
      font-weight: var(--fw-b);
    }
    &__text {
      text-align: right;
    }
  }
  @media (max-width: 1000px) {
    .chars {
      &__list {
        display: block;
      }
      &__item:nth-child(4n+1) {
        background-color: unset;
      }
      &__item:nth-child(4n+2) {
        background-color: unset;
      }
      &__item:nth-child(odd) {
        background-color: var(--clr-bg-gray);
      }
    }
  }
}

.art-prof-acc {
  .num {
    &__header {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      gap: 15px 30px;
      margin-block-end: 20px;
    }
    &__title {
      margin-block-end: 0;
    }
    &__search-wrap {
      position: relative;
      max-inline-size: 470px;
      inline-size: 100%;
      svg {
        position: absolute;
        inset-inline-end: clamp(15px, 0.8199rem + 0.5882vw, 20px);
        inset-block-start: 50%;
        transform: translateY(-50%);
        inline-size: 24px;
        block-size: 24px;
        fill: var(--clr-accent);
      }
      svg.close {
        cursor: pointer;
      }
      svg.hidden {
        display: none;
      }
    }
    &__search {
      inline-size: 100%;
      inline-size: 100%;
      padding: clamp(0.75rem, 0.6088rem + 0.7059vw, 1.125rem) 44px clamp(0.75rem, 0.6088rem + 0.7059vw, 1.125rem) clamp(0.75rem, 0.5618rem + 0.9412vw, 1.25rem);
      border: 1px solid var(--clr-border);
    }
    &__search:focus-visible {
      background-color: #F4F4F4;
      outline: none;
    }
    &__search::placeholder {
      color: var(--clr-light-text);
      font-size: 14px;
    }
    &__search:focus-visible::placeholder {
      color: transparent;
    }
    &__list-wrap {
      overflow-y: auto;
      overflow-x: hidden;
      max-block-size: 380px;
    }
    &__list-wrap::-webkit-scrollbar {
      width: clamp(3px, 0.1169rem + 0.3529vw, 6px);
    }
    &__list-wrap::-webkit-scrollbar-track {
      background-color: var(--clr-bg-gray);
      border-radius: 100vw;
    }
    &__list-wrap::-webkit-scrollbar-thumb {
      background-color: var(--clr-accent);
      border-radius: 100vw;
    }
    &__list {
      margin: 0 clamp(0px, -0.3529rem + 1.7647vw, 15px) 0 0;
      padding: 0;
      list-style: none;
    }
    &__item {
      display: flex;
      justify-content: space-between;
      row-gap: 15px;
      margin-block-start: 0;
      padding: clamp(15px, 0.8199rem + 0.5882vw, 20px) clamp(15px, 0.5846rem + 1.7647vw, 30px);
      font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
    }
    &__item.hidden {
      display: none;
    }
    &__item:nth-child(odd) {
      background-color: var(--clr-bg-gray);
    }
    &__heading {
      font-weight: var(--fw-b);
    }
    &__link {
      color: var(--clr-text);
      transition: color var(--tr);
    }
    &__link:hover {
      color: var(--clr-accent);
    }
  }
}

.art-prof-acc {
  .auto {
    &__title {
      margin-block-end: 20px;
    }
  }
}
