.tabs-faq {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  margin-block-end: 60px;
  &__placeholder {
    grid-column: 1 / span 5;
    display: none;
  }
  &__tabs {
    position: sticky;
    inset-block-start: 100px;
    grid-column: 1 / span 5;
    align-self: start;
    display: flex;
    flex-direction: column;
    max-inline-size: clamp(320px, 9.533rem + 18.5874vw, 370px);
  }
  &__tab {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: clamp(15px, 0.8199rem + 0.5882vw, 20px) clamp(25px, 1.2096rem + 1.7647vw, 40px);
    font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
    border-inline-start: 1px solid var(--clr-border);
    border-inline-end: 1px solid var(--clr-border);
    border-block-start: 1px solid var(--clr-border);
  }
  .tabs-faq__tab[aria-selected="true"] {
    position: relative;
    color: var(--clr-text);
    background-color: var(--clr-bg-gray);
    font-weight: var(--fw-b);
    border: 1px solid var(--clr-bg-gray);
  }
  .tabs-faq__tab[aria-selected="true"]::before {
    content: url('../img/svg/misc/tabs-faq-triangle.svg');
    position: absolute;
    inset-block-start: 0;
    inset-inline-end: 0;
    transform: translateX(calc(100% + 0px));
    display: block;
    aspect-ratio: 1 / 2.6;
    block-size: 112%;
  }
  &__tab:last-child {
    color: var(--clr-white);
    background-color: var(--clr-gray);
    border: 1px solid var(--clr-gray);
  }
  &__panels {
    grid-column: 6 / span 7;
  }
  &__panel {
    // display: none;
    h3 {
      margin-block-end: clamp(20px, 1.0147rem + 1.1765vw, 30px);
    }
  }
  &__panel>p+p {
    margin-block-start: 20px;
  }
}

@media (max-width: 900px) {
  .tabs-faq {
    grid-template-columns: 1fr;
    &__placeholder {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-inline: -15px;
      padding: clamp(15px, 0.8199rem + 0.5882vw, 20px) clamp(15px, 0.3493rem + 2.9412vw, 40px);
      background-color: var(--clr-bg-gray);
      font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
      font-weight: var(--fw-b);
      svg {
        inline-size: 20px;
        block-size: 20px;
        fill: var(--clr-accent);
        transition: transform var(--tr);
      }
    }
    &__placeholder.active {
      svg {
        transform: rotate(-180deg);
      }
    }
    &__placeholder.fixed {
      position: fixed;
      z-index: 200;
      top: 40px;
      inline-size: 100%;
      margin-inline-start: -15px;
      padding-inline-start: 15px;
    }
    &__tabs {
      position: static;
      overflow: hidden;
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      max-inline-size: unset;
      max-block-size: 0;
      margin-inline: -15px;
      margin-block-end: 30px;
      transition: max-block-size var(--tr);
    }
    &__tabs.active {
      max-block-size: 500px;
    }
    &__tab {
      border-inline-start: none;
      border-inline-end: none;
    }
    &__tab.hidden {
      display: none;
    }
    &__panels {
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
    }
    .tabs-faq__tab[aria-selected="true"] {
      background-color: var(--clr-white);
      font-weight: var(--fw-r);
    }
    .tabs-faq__tab[aria-selected="true"]::before {
      content: unset;
    }
  }
}
