.select {
  position: relative;
  z-index: 1;
  background-color: var(--clr-white);
}

.option {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
  inline-size: 100%;
  background-color: var(--clr-white);
  font-size: 14px;
  cursor: pointer;
  .label {
    display: inline;
    cursor: pointer;
  }
  input {
    position: fixed;
    transform: scale(0);
  }
}

.option.radio:first-child {
  visibility: hidden;
  block-size: 0;
  margin-block-start: -10px;
}

.option.radio.hidden {
  position: fixed;
  transform: scale(0);
}

.option.title {
  padding: 15px 20px;
  border: 1px solid var(--clr-border);
  transition: background-color var(--tr);
  .cur {
    flex-grow: 1;
  }
  svg {
    inline-size: 16px;
    block-size: 16px;
    fill: var(--clr-accent);
    transition: transform var(--tr);
  }
}

.option.title.active {
  background-color: var(--clr-bg-gray);
  svg {
    transform: rotate(-180deg);
  }
}

.options {
  overflow: hidden;
  position: absolute;
  inset-inline-start: 0;
  inset-block-start: 100%;
  inline-size: 100%;
  max-block-size: 0;
  background-color: var(--clr-white);
  transition: max-block-size var(--tr);
}

.options.open {
  max-block-size: 200px;
  border-inline-start: 1px solid var(--clr-border);
  border-inline-end: 1px solid var(--clr-border);
  border-block-end: 1px solid var(--clr-border);
}

.options__inner {
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-block-size: 125px;
  margin: 20px;
}

.options__inner::-webkit-scrollbar {
  width: 6px;
}

.options__inner::-webkit-scrollbar-track {
  background-color: var(--clr-bg-gray);
  border-radius: 0vw;
}

.options__inner::-webkit-scrollbar-thumb {
  background-color: var(--clr-accent);
  border-radius: 0vw;
}
