.ecology {
  margin-block-end: clamp(60px, 3.3971rem + 1.7647vw, 75px);
  &__title-block {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: min-content 1fr;
    column-gap: 30px;
    margin-block-end: 20px;
  }
  &__imgs {
    grid-column: 7 / span 6;
    grid-row: 1 / span 2;
    justify-self: end;
    margin-block: -20px 0;
  }
  &__title {
    grid-column: 1 / span 6;
    grid-row: 1 / span 1;
    margin-block-end: 30px;
  }
  &__title-text {
    grid-column: 1 / span 6;
    grid-row: 2 / span 1;
  }
  &__desc>p+p {
    margin-block-start: 20px;
  }
}

@media (max-width: 900px) {
  .ecology {
    &__imgs {
      margin-block: 0;
    }
  }
}

@media (max-width: 768px) {
  .ecology {
    &__title-block {
      column-gap: unset;
    }
    &__imgs {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      justify-self: center;
      margin-block-end: clamp(20px, 0.8036rem + 2.2321vw, 30px);
    }
    &__title {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      margin-block-end: clamp(15px, 0.2679rem + 3.3482vw, 30px);
    }
    &__title-text {
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
    }
  }
}
