.ac {
  // box-sizing: border-box;
}

.ac .ac-header {
  margin: 0;
  padding: 0;
}

.ac.is-active .ac-header {}

.ac .ac-trigger {
  text-align: left;
  width: 100%;
  padding: 0px 50px 0px 0px;
  display: block;
  cursor: pointer;
  background-color: transparent;
  transition: color .25s ease;
  position: relative;
  text-decoration: none;
  margin: 0;
  border: 0;
}

