.acc-search-results {
  margin-block-end: clamp(40px, 4.2206rem + -2.3529vw, 60px);
  .ac .ac-header {
    padding-block-end: clamp(20px, 1.0147rem + 1.1765vw, 30px);
    border-block-end: 1px solid var(--clr-border);
  }
  .ac .ac-trigger {
    color: var(--clr-text);
    .num {
      display: inline-block;
      font-size: 18px;
      font-weight: var(--fw-r);
      transform: translateY(clamp(-5px, 0.1176rem + -0.5882vw, 0rem));
    }
    .num::before {
      content: '\\';
      display: inline-block;
      margin-inline: 10px;
      color: var(--clr-accent);
    }
  }
  .ac .ac-trigger::after {
    content: url('../img/svg/icons/arrow-acc.svg');
    position: absolute;
    right: 10px;
    top: 50%;
    inline-size: clamp(1.25rem, 1.0147rem + 1.1765vw, 1.875rem);
    block-size: clamp(1.25rem, 1.0147rem + 1.1765vw, 1.875rem);
    transform: translate(0, -50%) rotate(0deg);
    transition: transform var(--tr);
  }
  .ac-panel>.grid-search-results+.grid-search-results {
    margin-block-start: clamp(15px, 0.8199rem + 0.5882vw, 20px);
  }
  .ac-panel>.grid-search-results:nth-child(1) {
    margin-block-start: clamp(20px, 1.0147rem + 1.1765vw, 30px);
  }
  .ac-panel>.grid-search-results:nth-last-child(1) {
    margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
  }
  .grid-search-results__item {
    font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
  }
  .ac .ac-panel {
    margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
    border-block-end: 1px solid var(--clr-border);
  }
}

@media (max-width: 500px) {
  .acc-search-results {
    .grid-search-results {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      column-gap: clamp(15px, 0.5846rem + 1.7647vw, 30px);
      &__item {
        align-self: center;
        font-size: clamp(16px, 0.9059rem + 0.4706vw, 20px);
        line-height: 1.5;
        img {
          max-inline-size: 170px;
        }
      }
      &__item:nth-child(1) {
        grid-column: 1 / span 5;
        grid-row: 1 / span 2;
      }
      &__item:nth-child(2) {
        grid-column: 6 / -1;
        grid-row: 1 / span 1;
      }
      &__item:nth-child(3) {
        grid-column: 6 / -1;
        grid-row: 2 / span 1;
      }
    }
  }
}
