.form-group {
  position: relative;
  z-index: 0;
  &__label {
    display: none;
  }
  .input.ok {
    border-color: var(--clr-ok);
  }
  .input.failed {
    border-color: var(--clr-error);
  }
  &__error-text {
    color: var(--clr-error);
    font-size: 12px;
    line-height: 1.3;
    text-align: right;
  }
  &__error-icons {
    position: absolute;
    inset-inline-end: 20px;
    inset-block-start: 25px;
    transform: translateY(-50%);
    inline-size: 20px;
    block-size: 20px;
    svg {
      display: none;
      position: absolute;
      inset: 0;
      inline-size: 20px;
      block-size: 20px;
    }
    svg.active {
      display: block;
    }
    .yes {
      fill: #00C95C;
    }
    .no {
      fill: #DA0606;
    }
  }
}

.form-group.agreement {
  .form-group__error-text {
    position: absolute;
    inset-inline-start: 0px;
    inset-block-start: -21px;
    transform: translate(0px, 0px);
    padding-inline-start: 10px;
    font-size: 14px;
    color: var(--clr-white);
    background-color: var(--clr-error);
  }
  .form-group__error-text.light {
    inset-inline-start: 0px;
    inset-block-start: -21px;
    padding-inline-start: 0px;
    font-size: 14px;
    color: var(--clr-error);
    background-color: unset;
  }
}
