.page-contacts {
  background-image: url('../img/misc/page-contacts-bg.webp');
  background-attachment: fixed;
  background-size: cover;
  background-position: center bottom;
  background-repeat: no-repeat;
}

.page-contacts .main {
  overflow-x: hidden;
}

.page-contacts .container-contacts {
  position: relative;
}

.page-contacts .container-contacts::before {
  content: '';
  position: absolute;
  z-index: -1;
  display: block;
  inset: 0 -100% 0 -100%;
  background-color: var(--clr-white);
}
