.form-btm {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: start;
  gap: 20px;
  label {
    display: inline;
    color: var(--clr-white);
    font-size: 14px;
    cursor: pointer;
    pointer-events: none;
    a {
      color: inherit;
      text-decoration: underline;
      pointer-events: auto;
    }
    a:hover {
      text-decoration: none;
    }
  }
  label.dark {
    color: var(--clr-text);
  }
  .checkbox-block {
    position: relative;
    display: grid;
    grid-template-columns: 30px 1fr;
    align-items: center;
    column-gap: 10px;
    inline-size: min(250px, 100%);
    cursor: pointer;
    .icon-checkbox {
      position: relative;
      inline-size: 30px;
      block-size: 30px;
      background-color: var(--clr-white);
      border: 1px solid var(--clr-border);
    }
    .icon-checkbox::before {
      content: url('../img/svg/icons/check.svg');
      display: block;
      position: absolute;
      inset-inline-start: 50%;
      inset-block-start: 50%;
      transform: translate(-50%, -50%);
      inline-size: 20px;
      block-size: 20px;
    }
    .icon-checkbox.hidden::before {
      display: none;
    }
    .agree-checkbox {
      position: absolute;
      z-index: -1;
    }
  }
}
