.car {
  position: relative;
  padding-block: clamp(90px, 1.8603rem + 18.8235vw, 250px) clamp(90px, 3.9779rem + 8.2353vw, 160px);
  .container {
    position: relative;
  }
  .container::before {
    content: '';
    position: absolute;
    z-index: -1;
    inset-inline-start: 50%;
    inset-block-start: 50%;
    transform: translate(-50%, -50%);
    aspect-ratio: .5295 / 1;
    inline-size: clamp(127px, 4.5963rem + 16.7059vw, 269px);
    background-image: url('../img/svg/misc/car-shapes.svg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
  &__img {
    position: relative;
    aspect-ratio: 2.1243 / 1;
    max-inline-size: 854px;
    margin-inline: auto;
    img {
      position: absolute;
      inset-inline-start: 0;
      inset-block-start: 0;
    }
    img:nth-child(1) {
      // border: 1px solid;
    }
    img:nth-child(2) {
      opacity: 0;
    }
  }
  &__dot {
    position: absolute;
    z-index: 3;
    inline-size: 28px;
    block-size: 28px;
    border: 2px solid var(--clr-white);
    background-color: hsl(0 0% 100% / .5);
    border-radius: 100vw;
    box-shadow: 0 0 10px var(--clr-white);
    cursor: pointer;
    visibility: hidden;
  }
  &__dot.top {
    inset-inline-start: 55%;
    inset-block-start: 20%;
  }
  &__dot.right {
    inset-inline-end: 15%;
    inset-block-start: 25%;
  }
  &__dot.bottom {
    inset-inline-start: 60%;
    inset-block-end: 30%;
  }
  &__dot.left {
    inset-inline-start: 25%;
    inset-block-start: 40%;
  }
  &__dot.active {
    visibility: visible;
  }
}

.car::before {
  content: '';
  position: absolute;
  z-index: 1;
  inset-inline-start: 0;
  inset-block-start: 0;
  aspect-ratio: 4.8975 / 1;
  inline-size: 100%;
  background-image: url('../img/svg/misc/car-triangle.svg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.car-overlay {
  --p: clamp(10px, -0.3162rem + 4.7059vw, 50px);
  --p: 10px;
  display: none;
  position: relative;
  z-index: 100;
  inset-inline-start: 0;
  inset-block-start: 0;
  inline-size: 100vw;
  block-size: 100vh;
  background-color: transparent;
  &__list-wrap {
    position: absolute;
    display: none;
    grid-template-columns: 1fr min-content;
    grid-template-rows: min-content;
    gap: 0 clamp(10px, 0.1544rem + 2.3529vw, 30px);
  }
  &__list-wrap.top {
    inset-inline-start: var(--p);
    inset-block-start: var(--p);
    .car-overlay__img {
      order: 1;
    }
    span {
      order: 0;
      text-align: right;
    }
  }
  &__list-wrap.right {
    inset-inline-end: var(--p);
    inset-block-start: var(--p);
  }
  &__list-wrap.bottom {
    inset-inline-end: var(--p);
    inset-block-end: var(--p);
  }
  &__list-wrap.left {
    inset-inline-start: var(--p);
    inset-block-end: var(--p);
    .car-overlay__img {
      order: 1;
    }
    span {
      order: 0;
      text-align: right;
    }
  }
  &__list-wrap.active {
    display: grid;
  }
  &__list {
    overflow-y: auto;
    overflow-x: hidden;
    block-size: clamp(250px, 13.2721rem + 11.7647vw, 350px);
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__list-inner {
    display: grid;
    gap: 10px 0;
    padding: 10px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 0 10px;
    color: var(--clr-white);
  }
  &__title {
    display: block;
    max-inline-size: 190px;
    color: inherit;
    font-size: 16px;
  }
  &__item:hover {
    .car-overlay__img {
      filter: drop-shadow(0 0 8px var(--clr-white));
    }
    .car-overlay__title {
      text-shadow: 0 0 10px var(--clr-white), 0 0 5px var(--clr-white);
    }
  }
  &__img {
    max-inline-size: clamp(70px, 3.4338rem + 4.7059vw, 110px);
    img {
      clip-path: polygon(0 0, calc(100% - 12%) 0, 100% 100%, 12% 100%);
    }
  }
  &__close2 {
    display: none;
  }
  &__list::-webkit-scrollbar {
    width: 3px;
  }
  &__list::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 100vw;
  }
  &__list::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent);
    border-radius: 100vw;
  }
}

.car-overlay.active {
  display: block;
  position: fixed;
  inset: 0;
  background-color: hsl(0 0% 0% / .75);
}

@media (max-width: 500px) {
  .car-overlay {
    &__close {
      display: none;
    }
    &__close2 {
      position: absolute;
      inset-inline-end: 15px;
      inset-block-start: 15px;
      display: inline-flex;
    }
    &__list-wrap {
      grid-template-columns: 1fr;
      gap: 0;
    }
    &__list-wrap.top {
      inset-inline-start: unset;
      inset-inline-end: var(--p);
      inset-block-start: unset;
      inset-block-end: var(--p);
    }
    &__list-wrap.left {
      inset-inline-start: unset;
      inset-inline-end: var(--p);
    }
    &__list-wrap.right {
      inset-block-start: unset;
      inset-block-end: var(--p);
      .car-overlay__img {
        order: 1;
      }
      span {
        order: 0;
        text-align: right;
      }
    }
    &__list-wrap.bottom {
      .car-overlay__img {
        order: 1;
      }
      span {
        order: 0;
        text-align: right;
      }
    }
  }
}
