.art-potreb-intro {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  align-items: end;
  column-gap: 30px;
  margin-block: 30px clamp(80px, 3.8235rem + 5.8824vw, 130px);
  &__img {
    grid-column: 1 / span 6;
    max-inline-size: 470px;
  }
  &__desc {
    grid-column: 7 / span 6;
  }
  &__title {
    margin-block-end: 20px;
  }
  &__text {}
}

@media (max-width: 767px) {
  .art-potreb-intro {
    grid-template-columns: 1fr;
    column-gap: 0;
    row-gap: 20px;
    &__img {
      grid-column: 1 / -1;
    }
    &__desc {
      grid-column: 1 / -1;
    }
  }
}
