.art-potreb-props {
  overflow: hidden;
  margin-block-end: clamp(75px, 3.3934rem + 6.4706vw, 130px);
  position: relative;
  padding-block: clamp(110px, 0.9926rem + 29.4118vw, 360px) clamp(110px, 2.6397rem + 21.1765vw, 290px);
  background-attachment: fixed;
  background-size: cover;
  &__img {
    position: absolute;
    z-index: -1;
    inset: 0;
    img {
      block-size: 100%;
    }
  }
  .svg-top {
    position: absolute;
    z-index: 1;
    inset-inline-start: 0;
    inset-block-start: -1px;
  }
  .svg-btm {
    position: absolute;
    z-index: 1;
    inset-inline-start: 0;
    inset-block-end: -1px;
  }
  &__inner {
    position: relative;
    z-index: 1;
    max-inline-size: 570px;
    color: var(--clr-white);
  }
  &__title {
    margin-block-end: clamp(10px, -0.0809rem + 3.5294vw, 40px);
  }
  &__desc {
    margin-block-end: 60px;
    font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
  }
  &__desc>p+p {
    margin-block-start: 20px;
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__item {}
  &__heading {
    font-size: clamp(20px, 1.0147rem + 1.1765vw, 30px);
    font-weight: var(--fw-m);
  }
  &__text {
    margin-block-end: clamp(5px, 0.0772rem + 1.1765vw, 15px);
    font-size: clamp(14px, 0.7809rem + 0.4706vw, 18px);
  }
}

.art-potreb-props::before {
  content: '';
  position: absolute;
  z-index: 0;
  inset: 0;
  background: linear-gradient(90deg, rgba(31, 46, 54, 0.93) 30.12%, rgba(64, 83, 93, 0.00) 83.28%);
}

@media (max-width: 768px) {
  .art-potreb-props {
    background-position: 40% 0;
  }
}

@media (max-width: 390px) {
  .art-potreb-props {
    &__list {
      grid-template-columns: 1fr;
      column-gap: unset;
      row-gap: 30px;
    }
  }
}
