.page-404 {
  background-image: url('../img/misc/404-bg.webp');
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: right 13% top;
  .crumbs {
    &__item {
      color: var(--clr-white);
    }
    &__item:not(:last-child) {
      color: var(--clr-border);
    }
    &__item:not(:last-child):hover {
      color: var(--clr-white);
    }
  }
}

@media (max-width: 1600px) {
  .page-404 {
    background-position: right 30% top;
  }
}

@media (max-width: 680px) {
  .page-404 {
    background-image: url('../img/misc/404-bg-680.webp');
    background-position: right top;
    background-size: 100% auto;
    &__inner {
      display: grid;
      justify-content: center;
      h1 {
        margin-block-end: 5px;
        text-align: center;
      }
      p {
        margin-block-end: 30px;
      }
      .button {
        justify-self: center;
        margin-block-end: 115px;
      }
    }
  }
}

@media (max-width: 428px) {
  .page-404 {
    background-image: url(../img/misc/404-bg-mobile.webp);
    background-size: 768px 720px;
    background-position: center top -50px;
  }
}

@media (max-width: 360px) {
  .page-404 {
    background-position: center top -75px;
  }
}

@media screen and (orientation: landscape) and (max-width: 568px) {
  .page-404 {
    background-image: url(../img/misc/404-bg-mobile.webp);
    background-size: 768px 720px;
    background-position: center top -50px;
  }
}
