.art-potreb-features {
  position: relative;
  padding-block: clamp(200px, 9.4412rem + 15.2941vw, 330px) clamp(190px, 9.2868rem + 12.9412vw, 300px);
  background-color: var(--clr-bg-gray);
  background-repeat: no-repeat;
  background-position: 70% 50%;
  &__img {
    position: absolute;
    z-index: 0;
    inset-inline-end: 0;
    inset-block-start: 0;
    transform: translate(clamp(-170px, 3.625rem + -11.875vw, 20px), clamp(80px, 3.3529rem + 8.2353vw, 150px));
    img {
      aspect-ratio: 1.3313 / 1;
      inline-size: clamp(320px, 5.8824rem + 70.5882vw, 920px);
      block-size: auto;
    }
  }
  &__desc {
    position: relative;
    z-index: 2;
    color: var(--clr-white);
  }
  &__title {
    margin-block-end: clamp(60px, 2.8088rem + 4.7059vw, 100px);
  }
  &__list {
    display: flex;
    flex-direction: column;
    row-gap: clamp(30px, -0.9485rem + 14.1176vw, 150px);
    margin: 0 0 clamp(60px, 2.8088rem + 4.7059vw, 100px) 0;
    padding: 0;
    list-style: none;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  svg {
    inline-size: clamp(30px, 1.1691rem + 3.5294vw, 60px);
    block-size: clamp(30px, 1.1691rem + 3.5294vw, 60px);
    fill: var(--clr-accent);
  }
  &__content {}
  &__heading {
    margin-block-end: 10px;
    font-size: 20px;
    font-weight: var(--fw-b);
    line-height: 1.3;
  }
  &__text {
    font-size: 14px;
  }
  .svg-top {
    position: absolute;
    z-index: 3;
    inset-inline-start: 0;
    inset-block-start: -1px;
    inline-size: 100%;
    block-size: auto;
  }
  .svg-btm {
    position: absolute;
    z-index: 3;
    inset-inline-start: 0;
    inset-block-end: -1px;
    inline-size: 100%;
    block-size: auto;
  }
}

.art-potreb-features::before {
  content: '';
  position: absolute;
  z-index: 1;
  inset: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.7) 40%, rgba(0, 0, 0, 0.8) 65%, rgba(0, 0, 0, 1) 100%);
}

@media (max-width: 768px) {
  .art-potreb-features {
    background-size: 100% auto;
  }
}
