.form {
  overflow: hidden;
  position: relative;
  .success-overlay {
    position: absolute;
    z-index: 2;
    inset: 0;
    background-color: hsl(0 0% 0% / .75);
    background-color: var(--clr-bg-gray);
    background-color: var(--clr-white);
  }
  .success {
    position: absolute;
    z-index: 3;
    inset-inline-start: 50%;
    inset-block-start: 50%;
    transform: translate(-150%, -50%);
    inline-size: min(570px, 100%);
    padding: 30px;
    font-size: 21px;
    text-align: center;
    visibility: hidden;
    opacity: 0;
    transition: transform var(--tr), opacity var(--tr), visibility 0s linear .3s;
  }
  .success.active {
    visibility: visible;
    opacity: 1;
    transform: translate(-50%, -50%);
    transition: transform var(--tr), opacity var(--tr), visibility 0s linear 0s;
  }
}

.form-top {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  margin-block-end: 20px;
}

.form-request,
.form-contacts,
.form-page-partners,
.form-page-faq {
  .form-top {
     :nth-child(1),
     :nth-child(4),
     :nth-child(5) {
      grid-column: 1 / -1;
    }
  }
}

.form-partner {
  .form-top {
     :nth-child(1),
     :nth-child(2) {
      grid-column: 1 / -1;
    }
  }
}

.form-write-us {
  .form-top {
     :nth-child(1),
     :nth-child(4),
     :nth-child(5) {
      grid-column: 1 / -1;
    }
  }
}

@media (max-width: 600px) {
  .form-top {
    grid-template-columns: 1fr;
    gap: 5px;
  }
}
