.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}

.modal__content {
  background: #fff;
  padding: 20px;
}

.modal.open {
  display: flex;
}

.model-viewer-wrap {
  overflow: hidden;
}

.model-viewer-wrap #car {
  width: 100%;
  height: 500px;
  border: 3px solid grey;
  background-image: url("../img/svg/misc/red-stripe.svg");
  background-repeat: no-repeat;
  background-position: 40% 0;
}

.btn-show-filter {
  position: fixed;
  z-index: 100;
  display: grid;
  justify-content: center;
  align-items: center;
  inset-inline-end: 0;
  inset-block-start: 200px;
  transform: translateX(20px);
  padding: 10px 20px 10px 5px;
  background-color: #fff;
  border-top: 1px solid var(--clr-border);
  border-bottom: 1px solid var(--clr-border);
  border-left: 1px solid var(--clr-border);
  border-radius: 15px 0 0 15px;
  transition: transform var(--tr);
}

.btn-show-filter svg {
  inline-size: clamp(20px, 0.7794rem + 2.3529vw, 40px);
  block-size: clamp(20px, 0.7794rem + 2.3529vw, 40px);
  fill: var(--clr-accent);
  transform: rotate(180deg);
}

.btn-show-filter:hover {
  transform: translateX(0px);
}

.container {
  --p-i: 15px;
  inline-size: min(var(--max-container) + var(--p-i) * 2, 100%);
  margin: 0 auto;
  padding-inline: var(--p-i);
}

.auto-grid {
  --min-w: 250px;
  --max-w: 1fr;
  --c-g: 30px;
  --r-g: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(var(--min-w), 100%), var(--max-w)));
  column-gap: var(--c-g);
  row-gap: var(--r-g);
}

.pb-before-footer {
  padding-block-end: var(--pb-before-footer);
}

.mobile-menu-overlay {
  position: fixed;
  z-index: 1;
  background-color: hsla(0, 0%, 0%, 0);
  transition: background-color var(--tr);
}

.mobile-menu-overlay.active {
  inset: 0;
  background-color: hsla(0, 0%, 0%, 0.5);
  cursor: pointer;
}

.pos-r {
  position: relative;
}

.p0 {
  padding: 0;
}

.mt30 {
  margin-block-start: 30px;
}

.m0 {
  margin: 0;
}

.mb0 {
  margin-block-end: 0;
}

.mb10 {
  margin-bottom: 10px;
}

.mb15 {
  margin-bottom: 15px;
}

.mb20 {
  margin-bottom: 20px;
}

.mb30 {
  margin-bottom: clamp(20px, 1.0147rem + 1.1765vw, 30px);
}

.mb40 {
  margin-bottom: 40px;
}

.mt60 {
  margin-top: clamp(30px, 1.17rem + 3.53vw, 60px);
}

.mb60 {
  margin-bottom: clamp(30px, 1.17rem + 3.53vw, 60px);
}

.mb90 {
  margin-bottom: 90px;
}

.mb100 {
  margin-bottom: 100px;
}

.clr-white {
  color: var(--clr-white);
}

.bg-white {
  background-color: var(--clr-white);
}

.uc {
  text-transform: uppercase;
}

.uc-none {
  text-transform: none;
}

.tac {
  text-align: center;
}

.fixed {
  overflow-y: hidden;
}

.noscroll {
  position: fixed;
  top: var(--st, 0);
  inline-size: 100%;
  overflow-y: scroll;
}

.ofxh {
  overflow-x: hidden;
  overflow-y: auto;
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

.grow-1 {
  flex-grow: 1;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.space {
  display: grid;
  justify-content: center;
  align-items: center;
  block-size: 1000px;
}

.btns-test-block {
  display: flex;
  gap: 40px;
  padding: 50px;
}

.btns-test-block.dark {
  background-color: var(--clr-gray);
}

.map {
  margin-block-end: 40px;
  counter-reset: list;
}
.map__item {
  display: block;
  padding: 5px 0 5px 20px;
  color: var(--clr-text);
}
.map__item:hover {
  background-color: hsla(0, 100%, 50%, 0.1);
}
.map__item::before {
  counter-increment: list;
  content: counter(list) ". ";
}