.search-cross {
  --input-h: 48px;
  --skew: 12px;
  display: flex;
  &__group {
    position: relative;
    flex-grow: 1;
    block-size: var(--input-h);
    margin-inline-end: -5px;
    padding: 1px;
    background-color: var(--clr-border);
    clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, 0% 100%);
    label {
      display: none;
    }
    input {
      inline-size: 100%;
      block-size: 100%;
      padding-inline: 10px clamp(40px, 2.0294rem + 2.3529vw, 60px);
      ;
      color: var(--clr-text);
      background-color: var(--clr-white);
      font-size: 14px;
      line-height: 1.3;
      border: none;
      clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, 0% 100%);
    }
    input:focus-visible {
      outline: none;
    }
    input::placeholder {
      color: var(--clr-light-text);
    }
    input:focus-visible::placeholder {
      color: transparent;
    }
    svg {
      position: absolute;
      inset-inline-end: clamp(10px, 0.1544rem + 2.3529vw, 30px);
      inset-block-start: 50%;
      transform: translateY(-50%);
      inline-size: 18px;
      block-size: 18px;
      fill: var(--clr-accent)
    }
    svg.close {
      cursor: pointer;
    }
    svg.hidden {
      display: none;
    }
  }
  .button--cross {
    --h: var(--input-h);
    svg {
      display: none;
    }
  }
}

@media (max-width: 550px) {
  .search-cross {
    --skew: 0px;
    display: grid;
    row-gap: 10px;
    &__group {
      margin-inline-end: unset;
    }
    .button {
      justify-self: center;
    }
  }
}
