.faq-accordion {
  .ac-header {
    margin-block-end: 20px;
    padding-block-end: 20px;
    border-block-end: 1px solid var(--clr-border);
  }
  .ac-trigger {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    font-size: clamp(20px, 1.01rem + 1.18vw, 30px);
    .svg-arrow {
      inline-size: clamp(20px, 1.0147rem + 1.1765vw, 30px);
      block-size: clamp(20px, 1.0147rem + 1.1765vw, 30px);
      fill: var(--clr-accent);
      transition: transform var(--tr);
    }
    .svg-arrow.active {
      transform: rotate(180deg);
    }
  }
  &__desc {
    margin-block-end: 20px;
  }
  &__desc>p+p {
    margin-block-start: 20px;
  }
  &__heading {
    font-size: 21px;
    font-weight: var(--fw-m);
  }
}
