.prof-categ-benefits {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 30px;
  margin-block-end: clamp(30px, 1.4044rem + 2.3529vw, 50px);
  &__title {
    grid-column: 1 / span 4;
    margin-block-end: unset;
  }
  &__text {
    grid-column: 5 / -1;
  }
  &__text>p+p {
    margin-block-start: 20px;
  }
}

@media (max-width: 1000px) {
  .prof-categ-benefits {
    column-gap: unset;
    row-gap: 20px;
    &__title {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
    }
    &__text {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
  }
}
