.page-about {
  background-image: url('../img/misc/page-about-bg.webp');
  background-attachment: fixed;
  background-size: cover;
  background-position: center bottom;
  background-repeat: no-repeat;
}

.page-about .main {
  overflow-x: hidden;
}

.page-about .container-about {
  position: relative;
}

.page-about .container-about::before {
  content: '';
  position: absolute;
  z-index: -1;
  display: block;
  inset: 0 -100% 0 -100%;
  background-color: var(--clr-white);
}
