.press-wrap {
  padding-block: clamp(150px, 2.3162rem + 35.2941vw, 450px) 50px;
  background-color: #000;
  background-image: url('../img/svg/misc/press-bg.svg'), linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 100%), url('../img/misc/press-bg.webp');
  background-repeat: no-repeat;
  background-size: 100% auto, 100% 200px, clamp(31.25rem, 17.1324rem + 70.5882vw, 68.75rem) auto;
  background-position: left top, left bottom, right top;
  background-attachment: scroll, scroll, fixed;
}

.press-block {
  max-inline-size: 770px;
  color: var(--clr-white);
  &__title-main {
    margin-block-end: clamp(30px, 1.1691rem + 3.5294vw, 60px);
  }
  &__list {
    display: grid;
    gap: clamp(40px, 2.0294rem + 2.3529vw, 60px) 0;
    margin: 0 0 clamp(30px, 1.1691rem + 3.5294vw, 60px);
    padding: 0;
    list-style: none;
  }
  &__item:hover {
    .press-block__title {
      a {
        text-shadow: 0 0 40px hsl(0 0% 100%);
      }
    }
    .press-block__img::after {
      transform: translateX(0%);
    }
  }
  &__img {
    position: relative;
    display: block;
    max-inline-size: 170px;
    margin-block-end: clamp(20px, 1.0147rem + 1.1765vw, 30px);
    clip-path: polygon(0 0, calc(100% - 13%) 0, 100% 100%, 13% 100%);
  }
  &__img::after {
    content: '';
    position: absolute;
    inset: 0;
    background-color: hsl(351 80% 42% / .35);
    clip-path: polygon(0 0, calc(100% - 13%) 0, 100% 100%, 13% 100%);
    transform: translateX(-100%);
    transition: transform var(--tr);
  }
  &__title {
    margin-block-end: 10px;
    a {
      color: var(--clr-white);
      font-size: clamp(20px, 1.0147rem + 1.1765vw, 30px);
      font-weight: var(--fw-m);
      transition: text-shadow var(--tr);
    }
  }
  &__text {
    font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
  }
  &__text>p+p {
    margin-block-start: 20px;
  }
}
