.crumbs {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 10px;
  margin-block-end: 60px;
  margin-block-end: clamp(30px, calc(1.17rem + 3.53vw), 60px);
  padding-block-start: 60px;
  padding-block-start: clamp(30px, calc(1.17rem + 3.53vw), 60px);
  font-size: 14px;
  svg {
    inline-size: 20px;
    block-size: 20px;
    fill: var(--clr-accent);
    transform: translateY(-2px);
  }
  &__item:not(:last-child) {
    color: var(--clr-light-text);
    transition: color var(--tr);
  }
  &__item:not(:last-child):hover {
    color: var(--clr-accent);
  }
  &__item:not(:last-child)::after {
    content: "\\";
    display: inline-block;
    margin-inline-start: 10px;
    color: var(--clr-accent);
  }
}

@media (max-width: 600px) {
  .crumbs {
    svg+a {
      display: none;
    }
    &__item:nth-child(3)::before {
      content: "\\";
      display: inline-block;
      margin-inline-end: 10px;
      color: var(--clr-accent);
    }
  }
}
