.nav-category-wrap {
  z-index: 1000;
  display: grid;
  align-items: center;
  block-size: clamp(70px, 3.9985rem + 1.8824vw, 86px);
  background-color: #FCFCFC;
  .container {
    padding: 0;
    padding-inline: 50px;
    @media (max-width: 1000px) {
      padding-inline: 15px;
    }
  }
}

.nav-category-wrap.art-potreb-buy {
  margin-block-end: 90px;
  background-color: var(--clr-white);
  h2.container {
    margin-block-end: 50px;
    padding: 0;
    padding-inline: 0px;
  }
  .nav-category {
    padding-inline: 30px;
    @media (max-width: 1000px) {
      padding-inline: 0px;
    }
    .nav-category-list {
      gap: 10px;
    }
    .splide__arrow--prev {
      left: -10px;
    }
    .splide__arrow--next {
      right: -10px;
    }
  }
}

.nav-category {
  position: relative;
  overflow: hidden;
  &__inner {
    overflow: hidden;
    display: flex;
    justify-content: start;
    align-items: center;
    gap: clamp(20px, 0.8971rem + 1.7647vw, 35px);
    gap: 35px;
    inline-size: 100%;
  }
  &__item {
    flex-grow: 0;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: clamp(5px, 0.1949rem + 0.5882vw, 10px);
    color: var(--clr-text);
    font-size: 14px;
    text-transform: uppercase;
    transition: color var(--tr);
    .icon-nav {
      // aspect-ratio: 1 / 1;
      inline-size: clamp(40px, 2.2647rem + 1.1765vw, 50px);
      block-size: clamp(40px, 2.2647rem + 1.1765vw, 50px);
    }
  }
  &__item:hover {
    color: var(--clr-accent);
  }
  .splide__arrow--prev {
    left: 10px;
  }
  .splide__arrow--next {
    right: 10px;
  }
}

.nav-category::-webkit-scrollbar {
  display: none;
}

.nav-category {
  .splide__arrow {
    background: transparent;
    border: unset;
    border-radius: unset;
    display: flex;
    justify-content: center;
    opacity: 1;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: unset;
    width: unset;
    z-index: 1;
    svg {
      fill: var(--clr-accent);
      height: 40px;
      width: 40px;
    }
  }
}

.nav-category-list {
  gap: 35px;
}

.container.art-potreb-buy__title{
  padding-inline: var(--p-i) !important;
}
