.pot-tov-wrap {
  padding-block: 130px 200px;
  background-color: var(--clr-bg-2);
}

.pot-tov-title {
  margin-block-end: clamp(40px, -1.6176rem + 20.5882vw, 215px);
  font-size: clamp(24px, 1.2176rem + 1.4118vw, 36px);
  font-weight: var(--fw-m);
  line-height: 1.3;
  text-transform: uppercase;
}

.pot-tov-block {
  &__item {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 0 30px;
    margin-block-end: 30px;
    color: var(--clr-white);
    background-color: var(--clr-gray);
    border: 1px solid var(--clr-light-text);
  }
  &__img {
    grid-column: 7 / -1;
    grid-row: 1 / span 1;
    margin: 25px clamp(15px, -4.4356rem + 11.194vw, 60px) 0 0;
    clip-path: polygon(0 0, calc(100% - 12%) 0, 100% 100%, 12% 100%);
    transform: translateY(-60px);
  }
  &__desc {
    grid-column: 1 / span 6;
    grid-row: 1 / span 1;
    margin: clamp(15px, -4.4356rem + 11.194vw, 60px) 0 clamp(15px, -4.4356rem + 11.194vw, 60px) clamp(15px, -4.4356rem + 11.194vw, 60px);
  }
  &__title {
    margin-block-end: 10px;
    font-size: clamp(20px, 1.0147rem + 1.1765vw, 30px);
    font-weight: var(--fw-m);
    line-height: 1.3;
  }
  &__text {
    margin-block-end: 20px;
    font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
  }
  .button {}
}

.pot-tov-block__item.even {
  margin-block-end: clamp(55px, 2.2435rem + 2.4876vw, 65px);
  .pot-tov-block__img {
    grid-column: 1 / span 6;
    margin: 0 0 25px clamp(15px, -4.4356rem + 11.194vw, 60px);
    clip-path: polygon(0 0, calc(100% - 12%) 0, 100% 100%, 12% 100%);
    transform: translateY(60px);
  }
  .pot-tov-block__desc {
    grid-column: 7 / -1;
    // grid-row: 1 / span 1;
    margin: clamp(15px, -4.4356rem + 11.194vw, 60px) clamp(15px, -4.4356rem + 11.194vw, 60px) clamp(15px, -4.4356rem + 11.194vw, 60px) 0;
  }
}

@media (max-width: 768px) {
  .pot-tov-block {
    display: grid;
    gap: clamp(30px, 0.5357rem + 6.6964vw, 60px) 0;
    &__item {
      grid-template-columns: 1fr;
      gap: 20px 0;
      margin-block-end: unset;
    }
    &__img {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      justify-self: end;
      max-inline-size: clamp(183px, 3.0893rem + 41.7411vw, 370px);
      transform: unset;
      margin-block-start: clamp(-30px, 0.2679rem + -4.4643vw, -10px);
    }
    &__desc {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      margin: 0 15px 30px;
    }
    &__item.even {
      display: grid;
      grid-template-columns: 1fr;
      margin-block-end: unset;
      .pot-tov-block__img {
        justify-self: start;
        transform: unset;
        margin: clamp(-30px, 0.2679rem + -4.4643vw, -10px) 0 0 15px;
      }
      .pot-tov-block__desc {
        margin: 0 15px 30px;
      }
    }
  }
}
