.garantija-wrap {
  position: relative;
  z-index: 2;
  padding-block: clamp(60px, 2.8088rem + 4.7059vw, 100px) clamp(80px, 4.5294rem + 2.3529vw, 100px);
  background-color: var(--clr-bg-gray);
}

.garantija {
  position: relative;
  &__slogan {
    max-inline-size: clamp(138px, 4.8132rem + 19.0588vw, 300px);
    margin-block-end: clamp(40px, 0.8529rem + 8.2353vw, 110px);
    color: var(--clr-gray);
    font-size: clamp(12px, 0.5618rem + 0.9412vw, 20px);
  }
  &__block {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 0 30px;
  }
  &__title {
    grid-column: 1 / -1;
    grid-row: 1 / span 1;
    margin-block-end: 20px;
  }
  &__text {
    grid-column: 1 / span 6;
    grid-row: 2 / span 1;
    margin-block-end: clamp(30px, 2.7353rem + -1.1765vw, 40px);
  }
  &__text>p+p {
    margin-block-start: 20px;
  }
  &__list {
    grid-column: 7 / -1;
    grid-row: 2 / span 2;
    justify-self: end;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 30px;
    max-inline-size: 370px;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__item {}
  &__heading {
    display: block;
    margin-block-end: 10px;
    font-size: clamp(18px, 0.9838rem + 0.7059vw, 24px);
    font-weight: var(--fw-b);
    line-height: 1.3;
  }
  &__txt {
    font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
  }
  .button {
    grid-column: 1 / span 6;
    grid-row: 3 / span 1;
    justify-self: start;
  }
}

.garantija::before {
  content: '';
  position: absolute;
  inset-inline-start: 138px;
  inset-block-start: -218px;
  inset-inline-start: clamp(18px, -1.6985rem + 14.1176vw, 138px);
  inset-block-start: clamp(-218px, -1.7529rem + -16.2353vw, -80px);
  aspect-ratio: 1.9545 / 1;
  inline-size: clamp(275px, 8.4816rem + 43.5294vw, 645px);
  // block-size: 330px;
  background-image: url('../img/svg/misc/stripes.svg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

@media (max-width: 768px) {
  .garantija {
    &__block {
      gap: 0;
    }
    &__slogan {}
    &__title {}
    &__text {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
    &__list {
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
      justify-self: start;
      max-inline-size: 570px;
      margin-block-end: 30px;
    }
    .button {
      grid-column: 1 / -1;
      grid-row: 4 / span 1;
    }
  }
}
