.page-ecology {
  background-image: url('../img/misc/page-ecology-bg.webp');
  background-attachment: fixed;
  background-size: cover;
  background-position: center bottom clamp(-11.25rem, 51.25rem + -62.5vw, -5rem);
  background-repeat: no-repeat;
}

.page-ecology .main {
  overflow-x: hidden;
}

.page-ecology .container-ecology {
  position: relative;
}

.page-ecology .container-ecology::before {
  content: '';
  position: absolute;
  z-index: -1;
  display: block;
  inset: 0 -100% 0 -100%;
  background-color: var(--clr-white);
}

@media (max-width: 1600px) {
  .page-ecology {}
}

@media (max-width: 1024px) {
  .page-ecology {
    background-position: left 30% bottom -40px;
  }
}
