.card-press {
  display: grid;
  grid-template-columns: 168px 1fr max-content;
  align-items: end;
  column-gap: 30px;
  &__img {
    --h: 168px;
    --skew: calc(var(--h) / 10);
    inline-size: var(--h);
    clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, var(--skew) 100%);
    position: relative;
    img {
      // clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, var(--skew) 100%);
    }
  }
  &__img::after {
    content: '';
    position: absolute;
    inset: 0;
    background-color: rgba(193, 21, 47, 0.5);
    transform: translateX(-100%) skewX(8deg);
    transition: transform .4s ease-in-out;
  }
  // &__img:hover::after {
  //   transform: translateX(0%) skewX(8deg);
  // }
  &__title {
    margin-block-end: 10px;
    a {
      color: var(--clr-text);
    }
  }
  .button {
    --pi: 40px;
  }
}

.card-press:hover {
  .card-press__img::after {
    transform: translateX(0%) skewX(8deg);
    cursor: pointer;
  }
  .button {
    .label {
      color: var(--clr-hov);
    }
    svg {
      fill: var(--svg-fill-hov);
    }
    .icon {
      background-color: var(--bg-hov);
      transform: skew(var(--skew), 0deg);
      &:before,
      &:after {
        transition-delay: 0s;
        width: 30px;
        opacity: 1;
      }
      &:before {
        left: 0;
      }
      &:after {
        right: 0;
      }
      span {
        &:before,
        &:after {
          height: 30px;
          transition-delay: 0.45s;
        }
      }
    }
    .icon2 {
      transform: skew(var(--skew), 0deg) scaleX(0.2);
      background: var(--bg-hov);
      transition-delay: 0s;
      opacity: 0;
    }
  }
}

@media (max-width: 1000px) {
  .card-press {
    grid-template-columns: 168px 1fr;
    .button {
      display: none;
    }
  }
}

@media (max-width: 600px) {
  .card-press {
    grid-template-columns: 1fr;
    column-gap: unset;
    &__img {
      --h: 138px;
      margin-block-end: 20px;
    }
  }
}
