.search-zero-results {
  display: flex;
  gap: 20px;
  margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
  padding-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
  border-block-end: 1px solid var(--clr-border);
  &__text {
    max-inline-size: 700px;
  }
  svg {
    flex: 0 0 auto;
    inline-size: 60px;
    block-size: 60px;
    fill: var(--clr-accent);
  }
}
