.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: var(--fz-s);
  font-weight: var(--fw-m);
  text-transform: uppercase;
  clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, var(--skew) 100%);
}

.btn--dark {
  color: var(--clr-white);
  background-color: var(--clr-accent);
  transition: background-color var(--tr);
}

.btn--dark:hover {
  background-color: var(--clr-hover);
}

.btn--dark:active {
  background-color: var(--clr-pressed);
}

.btn--txt {
  --h: 48px;
  --skew: calc(var(--h) / 4);
  block-size: var(--h);
  padding-inline: 40px;
}

.btn--icon {
  --w: 76px;
  --h: 40px;
  --skew: calc(var(--h) / 2.5);
  inline-size: var(--w);
  block-size: var(--h);
  svg {
    inline-size: 24px;
    block-size: 24px;
    fill: white;
  }
  svg.hidden {
    display: none;
  }
}

/////////// ANIMATED BUTTONS //////////////////////
.button {
  --h: 48px;
  --pi: 30px;
  --clr: var(--clr-white);
  --clr-hov: var(--clr-text);
  --bg: var(--clr-accent);
  --bg-hov: transparent;
  --skew: 13deg;
  --bor-clr: transparent;
  --svg-w: 24px;
  --svg-h: 24px;
  --svg-fill: var(--clr-white);
  --svg-fill-hov: var(--clr-text);
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  block-size: var(--h);
  margin-inline: 10px;
  padding: 0 var(--pi);
  text-transform: uppercase;
  cursor: pointer;
  .label {
    font-size: 14px;
    z-index: 10;
    position: relative;
    color: var(--clr);
    white-space: nowrap;
    line-height: 1;
    transition: all 0.225s;
    // transition-delay: 0.45s;
  }
  svg {
    inline-size: var(--svg-w);
    block-size: var(--svg-h);
    fill: var(--svg-fill);
  }
  .icon2 {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 0.225s;
    transform: skew(var(--skew), 0deg) scaleX(1);
    background: var(--bg);
    border: 1px solid var(--bor-clr);
    transition-delay: 0.45s;
  }
  .icon {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 0.225s;
    transform: scale(0.9) skew(0deg, 0deg);
    transition-delay: 0.15s;
    &:before,
    &:after {
      width: 60px;
      opacity: 0;
      content: '';
      height: 1px;
      position: absolute;
      display: block;
      background: var(--clr-accent);
      transition: all 0.375s;
      transition-delay: 0.3s;
    }
    &:before {
      top: 0;
      left: calc(50% - 30px);
    }
    &:after {
      bottom: 0;
      right: calc(50% - 30px);
    }
    span {
      &:before,
      &:after {
        height: 0;
        transition-delay: 0s;
        content: '';
        background: var(--clr-accent);
        position: absolute;
        display: block;
        width: 2px;
        transition: all 0.225s;
      }
      &:before {
        left: 0;
        top: 0;
      }
      &:after {
        right: 0;
        bottom: 0;
      }
    }
  }
  &:hover {
    .label {
      color: var(--clr-hov);
    }
    svg {
      fill: var(--svg-fill-hov);
    }
    .icon {
      background-color: var(--bg-hov);
      transform: skew(var(--skew), 0deg);
      &:before,
      &:after {
        transition-delay: 0s;
        width: 30px;
        opacity: 1;
      }
      &:before {
        left: 0;
      }
      &:after {
        right: 0;
      }
      span {
        &:before,
        &:after {
          height: 30px;
          transition-delay: 0.45s;
        }
      }
    }
    .icon2 {
      transform: skew(var(--skew), 0deg) scaleX(0.2);
      background: var(--bg-hov);
      transition-delay: 0s;
      opacity: 0;
    }
  }
}

.button.ondark {
  --clr: var(--clr-white);
  --clr-hov: var(--clr-white);
  // --bg: var(--clr-accent-10);
  // --bor-clr: transparent;
}

.button.ghost {
  --clr: var(--clr-text);
  --bg: transparent;
  --bor-clr: var(--clr-accent);
}

.button.ghost.ondark {
  --clr: var(--clr-white);
  --bg: transparent;
  --bor-clr: var(--clr-accent);
}

.button.light {
  --clr: var(--clr-text);
  --clr-hov: var(--clr-text);
  --bg: var(--clr-accent-10);
  --bor-clr: transparent;
}

.button.light.ondark {
  --clr: var(--clr-white);
  --clr-hov: var(--clr-white);
  --bg: var(--clr-accent-10);
  --bor-clr: transparent;
}

.button.symb {
  --h: 38px;
  --pi: 20px;
  --bg: var(--clr-accent);
  --bg-hov: var(--clr-hover);
  --bor-clr: transparent;
  --skew: 20deg;
  --svg-fill-hov: var(--clr-white);
}
