.create-future {
  padding-block: clamp(140px, 7.3382rem + 7.0588vw, 200px) clamp(60px, 0.4559rem + 16.4706vw, 200px);
  color: var(--clr-white);
  background-image: url('../img/svg/misc/top-right-white-triangle-bg.svg');
  background-repeat: no-repeat;
  background-position: clamp(150px, -2.4375rem + 59.0625vw, 1095px) top;
  background-size: 825px 312px;
  &__title {
    margin-block-end: 20px;
  }
  &__desc {
    max-inline-size: 770px;
  }
  &__desc>p+p {
    margin-block-start: 20px;
  }
}
