:root {
  --max-container: 1170px;
  --ff-prim: "Ubuntu", system-ui, sans-serif;
  --ff-sec: "Exo2", system-ui, sans-serif;
  --fz-base: 18px;
  --fz-s: 14px;
  --fw-l: 300;
  --fw-r: 400;
  --fw-m: 500;
  --fw-b: 700;
  --clr-accent: #BF152F;
  --clr-accent-10: hsl(351 80% 42% / .1);
  --clr-hover: #D92944;
  --clr-pressed: #A3031B;
  --clr-border: #D9D9D9;
  --clr-error: #DA0606;
  --clr-ok: #00C95C;
  --clr-red: #BF152F;
  --clr-success: #00C95C;
  --clr-text: #293738;
  --clr-light-text: #696969;
  --clr-gray: #40535D;
  --clr-bg-gray: #F1F1F1;
  --clr-bg-2: #DADDDF;
  --clr-bg-light: #FCFCFC;
  --clr-white: #fff;
  --clr-grad: linear-gradient(90deg, #2A404C 2.34%, #40535D 85.57%);
  --tr: .3s ease-in-out;
  --pb-before-footer: 680px;
}
