.news-single {
  display: flex;
  flex-direction: column;
  row-gap: 40px;
  margin-block-end: clamp(40px, 2.03rem + 2.35vw, 60px);
  padding-block-end: clamp(40px, 2.03rem + 2.35vw, 60px);
  border-block-end: 1px solid var(--clr-border);
  &__title-block {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
  }
  &__img {
    grid-column: 1 / span 4;
    grid-row: 1 / span 2;
    aspect-ratio: 1.2333 / 1;
    inline-size: min(370px, 100%);
    block-size: auto;
  }
  &__title {
    grid-column: 5 / span 8;
    grid-row: 1 / span 1;
  }
  &__socials {
    align-self: end;
    grid-column: 5 / span 8;
    grid-row: 2 / span 1;
    display: flex;
    align-items: center;
    column-gap: 20px;
    line-height: 1;
    svg {
      inline-size: 30px;
      block-size: 30px;
      fill: var(--clr-accent);
      transition: fill var(--tr);
    }
    svg:hover {
      fill: var(--clr-hover)
    }
  }
  &__desc>p+p {
    margin-block-start: 20px;
  }
  &__btm {
    display: flex;
    align-items: center;
    column-gap: 13px;
    color: var(--clr-accent);
    transition: color var(--tr);
    svg {
      inline-size: 40px;
      block-size: 40px;
      fill: var(--clr-accent);
      transition: fill var(--tr);
    }
  }
  &__btm:hover {
    color: var(--clr-hover);
    svg {
      fill: var(--clr-hover);
    }
  }
}

@media (max-width: 800px) {
  .news-single {
    &__title-block {
      row-gap: 20px;
    }
    &__img {
      grid-column: 1 / span 4;
      grid-row: 1 / span 1;
    }
    &__title {
      grid-column: 1 / span 12;
      grid-row: 2 / span 1;
      margin-block-end: unset;
    }
    &__socials {
      align-self: center;
      grid-column: 5 / span 8;
      grid-row: 1 / span 1;
    }
  }
}

@media (max-width: 500px) {
  .news-single {
    &__title-block {
      column-gap: unset;
    }
    &__img {
      grid-column: 1 / span 12;
      grid-row: 1 / span 1;
      max-inline-size: 290px;
    }
    &__title {
      grid-column: 1 / span 12;
      grid-row: 2 / span 1;
      margin-block-end: unset;
    }
    &__socials {
      align-self: unset;
      grid-column: 1 / span 12;
      grid-row: 3 / span 1;
    }
  }
}
