.pagination {
  display: flex;
  justify-content: end;
  align-items: center;
  column-gap: 0px;
  font-size: 20px;
  &__prev,
  &__next {
    svg {
      inline-size: 26px;
      block-size: 26px;
      fill: var(--clr-accent);
    }
  }
  &__next {
    margin-inline-start: 10px;
  }
  &__prev {
    margin-inline-end: 10px;
    svg {
      transform: scale(-1, 1);
    }
  }
  &__item {
    --h: 26px;
    --skew: calc(var(--h) / 4);
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--clr-text);
    inline-size: 41px;
    block-size: var(--h);
    clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, var(--skew) 100%);
    transition: color var(--tr), background-color var(--tr);
  }
  &__item:visited {
    color: var(--clr-text);
  }
  &__item.active {
    color: var(--clr-white);
    background-color: var(--clr-accent);
  }
  &__item:not(:first-child, :last-child, span):hover {
    color: var(--clr-white);
    background-color: var(--clr-hover);
  }
}

@media (max-width: 500px) {
  .pagination {
    justify-content: center;
     :nth-child(4),
    :nth-child(5),
    :nth-child(6) {
      display: none;
    }
  }
}
