.popup {
  --sp: 10px;
  z-index: 10000;
  overflow: hidden;
  max-inline-size: calc(100% - var(--sp));
  max-block-size: calc(100% - var(--sp));
  inline-size: min(calc(690px + var(--sp)), 100%);
  padding: 0;
  background-color: transparent;
  border: none;
  &__inner {
    position: relative;
    inline-size: 100%;
    // block-size: 100%;
    padding: 60px 60px;
    padding: clamp(30px, 1.17rem + 3.53vw, 60px) clamp(15px, -0.12rem + 5.29vw, 60px);
    background-color: var(--clr-bg-gray);
  }
  h3 {
    margin-block-end: 20px;
  }
  &__form {
    max-block-size: calc(100vh - 200px);
    block-size: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    padding-inline-end: 3px;
    border-inline-end: 3px solid var(--clr-bg-gray);
  }
  .close {
    position: relative;
    left: 100%;
    transform: translateX(-100%);
  }
}

@media (max-width: 600px) {
  .popup {
    --sp: 0px;
    &__form {
      max-block-size: calc(100vh - 147px);
    }
  }
}

dialog::backdrop {
  background-color: hsl(0 0% 0% / .75);
}

.popup__form::-webkit-scrollbar {
  width: 3px;
}

.popup__form::-webkit-scrollbar-track {
  background-color: var(--clr-white);
  border-radius: 100vw;
}

.popup__form::-webkit-scrollbar-thumb {
  background-color: var(--clr-red);
  border-radius: 100vw;
}

.popup-filter {
  inline-size: min(calc(1170px + var(--sp)), 100%);
  .popup__inner {
    padding: 0;
    .tabs-acc__tabs {
      background-color: unset !important;
    }
  }
}
