html {
  overflow-y: auto;
  overflow-x: hidden;
}

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  font-family: var(--ff-prim);
  font-size: var(--fz-base);
  line-height: 1.4;
  color: var(--clr-text);
  background-color: var(--clr-white);
}

main {
  position: relative;
  z-index: 0;
}
