.slider-history {
  padding-block-end: clamp(60px, 1.3971rem + 11.7647vw, 160px);
  background-color: var(--clr-white);
  .slider {
    overflow: hidden;
    margin-block-end: 40px;
  }
  .track {
    display: flex;
  }
  .slide {
    position: relative;
    flex: 0 0 auto;
    inline-size: 770px;
    block-size: 260px;
  }
  .content {
    position: absolute;
    inset: 0;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    gap: 0 30px;
  }
  &__img {
    position: relative;
    justify-self: end;
    grid-column: 1 / span 3;
    inline-size: clamp(180px, 9.6029rem + 8.2353vw, 250px);
    block-size: clamp(142px, 7.5809rem + 6.4706vw, 197px);
    margin-block-start: 20px;
    img {
      clip-path: polygon(0 0, calc(100% - 13%) 0, 100% 100%, 13% 100%);
    }
  }
  &__img::before {
    content: '';
    position: absolute;
    inset-inline-start: -10px;
    inset-block-start: -20px;
    inline-size: 5px;
    block-size: 107px;
    background-color: var(--clr-accent);
    transform: skewX(9.5deg);
  }
  &__img::after {
    content: '';
    position: absolute;
    inset-inline-end: 15px;
    inset-block-end: -30px;
    inline-size: 5px;
    block-size: 107px;
    background-color: var(--clr-accent);
    transform: skewX(9.5deg);
  }
  &__desc {
    grid-column: 4 / -1;
    align-self: end;
  }
  &__heading {
    margin-block-end: 10px;
    color: var(--clr-gray);
    font-size: clamp(18px, 0.9838rem + 0.7059vw, 24px);
    font-weight: var(--fw-b);
    line-height: 1.3;
  }
  &__text {
    font-size: clamp(12px, 0.7029rem + 0.2353vw, 14px);
    line-height: 1.3;
  }
  .scrollbar {
    position: relative;
    inline-size: 770px;
    block-size: 2px;
    margin-inline: auto;
    background-color: var(--clr-border);
  }
  .thumb {
    position: absolute;
    inset-inline-start: 0;
    inset-block-start: 50%;
    transform: translate(-50%, -50%);
    inline-size: 35px;
    block-size: 16px;
    background-color: var(--clr-accent);
  }
  .dates {
    overflow: hidden;
    position: relative;
    block-size: clamp(83px, 4.1993rem + 4.9412vw, 125px);
    margin-inline: auto;
    margin-block-end: 0px;
    // outline: 1px solid;
  }
  .dates__track {
    position: absolute;
    inset: 0;
  }
  .dates__item {
    display: flex;
    justify-content: center;
    align-items: center;
    block-size: 100%;
    color: var(--clr-gray);
    font-family: var(--ff-sec);
    font-size: clamp(64px, 3.2471rem + 3.7647vw, 96px);
    font-weight: var(--fw-b);
    line-height: 1.3;
  }
}

@media (max-width: 992px) {
  .slider-history {
    .slide {
      inline-size: 100%;
      block-size: auto;
    }
    .content {
      position: static;
      gap: 40px 0;
      margin-inline: 15px;
    }
    &__img {
      justify-self: center;
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
    }
    &__desc {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
    .scrollbar {
      inline-size: 80%;
    }
  }
}
