.menu-extra-wrap {
  z-index: 2000;
  padding-block: 10px;
  background-color: #FCFCFC;
  border-block-start: 1px solid var(--clr-border);
  border-block-end: 1px solid var(--clr-border);
  .container {
    display: grid;
  }
}

.menu-extra {
  justify-self: center;
  display: flex;
  justify-content: space-between;
  max-inline-size: 369px;
  inline-size: 100%;
  &__item {
    color: var(--clr-text);
    font-size: clamp(12px, 0.7029rem + 0.2353vw, 14px);
    line-height: 1;
    text-transform: uppercase;
    transition: color var(--tr);
  }
  &__item:visited {
    color: var(--clr-text);
  }
  &__item:hover {
    color: var(--clr-accent);
  }
  &__item.active {
    color: var(--clr-accent);
  }
}

@media (max-width: 768px) {
  .menu-extra-wrap {
    background-color: hsl(0 0% 99% / .95);
  }
}
