.garantii {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 30px;
  margin-block-end: clamp(45px, 1.99rem + 4.12vw, 80px);
  &__title {
    grid-column: 1 / span 6;
    grid-row: 1 / span 1;
    margin-block-end: clamp(15px, 0.58rem + 1.76vw, 30px);
  }
  &__imgs {
    justify-self: end;
    grid-column: 7 / span 6;
    grid-row: 1 / span 2;
    margin-block-start: -30px;
    .imgs-couple__img {
      visibility: hidden;
    }
  }
  &__desc {
    grid-column: 1 / span 6;
    grid-row: 2 / span 3;
  }
  &__desc>p+p {
    margin-block-start: 20px;
  }
  &__stats {
    grid-column: 7 / span 6;
    grid-row: 3 / span 1;
  }
  &__stats-intro {
    margin-block-end: 45px;
  }
  &__stats-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    margin: 0;
    padding: 0;
  }
  &__stats-item {
    display: grid;
    grid-template-columns: 40px 1fr;
    column-gap: 10px;
    svg {
      inline-size: 40px;
      block-size: 40px;
      fill: var(--clr-accent);
    }
  }
  &__stats-intro {
    margin-block-end: clamp(30px, 1.52rem + 1.76vw, 45px);
  }
  &__stats-desc {
    display: flex;
    flex-direction: column;
  }
  &__stats-title {
    font-size: clamp(18px, 0.98rem + 0.71vw, 24px);
    font-weight: var(--fw-b);
  }
}

@media (max-width: 1000px) {
  .garantii {
    grid-template-columns: 1fr;
    &__title {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
    }
    &__desc {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      margin-block-end: 50px;
    }
    &__imgs {
      justify-self: center;
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
      margin-block-start: unset;
    }
    &__stats {
      grid-column: 1 / -1;
      grid-row: 4 / span 1;
    }
  }
}

@media (max-width: 600px) {
  .garantii {
    &__stats-list {
      align-items: start;
      column-gap: 15px;
    }
    &__stats-item {
      grid-template-columns: 1fr;
      column-gap: unset;
      svg {
        margin-block-end: 10px;
      }
    }
    &__stats-title {
      margin-block-end: 5px;
    }
  }
}
