.search-failed {
  display: flex;
  flex-wrap: wrap;
  justify-content: end;
  gap: 30px;
  margin-block-end: clamp(0rem, 5.1618rem + -7.0588vw, 60px);
  p {
    max-inline-size: 470px;
  }
}

@media (max-width: 768px) {
  .search-failed {
    display: block;
    p {
      max-inline-size: unset;
      margin-block-end: 30px;
    }
  }
}
