.production-wrap {
  overflow: hidden;
  position: relative;
}

.production-wrap__triangle {
  position: absolute;
  z-index: 1;
  inset-inline-end: -5px;
  inset-block-start: -1px;
  background-image: url('../img/svg/misc/production-triangle.svg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  inline-size: clamp(152px, -0.6882rem + 50.9412vw, 585px);
  block-size: clamp(119px, -0.7507rem + 40.9412vw, 468px);
}

.production-wrap__triangle2 {
  position: absolute;
  z-index: 1;
  inset-inline-start: -5px;
  inset-block-end: -1px;
  background-image: url('../img/svg/misc/production-triangle-2.svg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  inline-size: clamp(153px, 4.9507rem + 23.0588vw, 349px);
  block-size: clamp(120px, 3.5941rem + 19.5294vw, 286px);
}

.production {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  &__item {
    --opacity: .2;
    --pbs: clamp(100px, 3.75rem + 12.5vw, 300px);
    flex: 0 0 auto;
    position: relative;
    inline-size: 100vw;
    block-size: clamp(560px, 29.25rem + 28.75vw, 1020px);
    padding-block-start: var(--pbs);
  }
  &__item:nth-child(2) {
    --opacity: .4;
    //  --pbs: 420px;
    .production__desc {
      // margin-inline-start: auto;
    }
  }
  &__item:nth-child(3) {
    --opacity: .7;
  }
  &__img {
    position: absolute;
    z-index: -1;
    inset-inline-start: 50%;
    inset-block-start: 50%;
    transform: translate(-50%, -50%);
    inline-size: 100%;
    block-size: 100%;
    img {
      block-size: 100%;
    }
  }
  &__img::after {
    content: '';
    position: absolute;
    inset: 0;
    background-color: #000;
    opacity: var(--opacity);
  }
  &__desc {
    max-inline-size: 570px;
    color: var(--clr-white);
  }
  &__title {
    margin-block-end: 20px;
    font-size: clamp(24px, 1.2176rem + 1.4118vw, 36px);
  }
  &__text {
    margin-block-end: 30px;
  }
  &__text>p+p {
    margin-block-start: 20px;
  }
  .button {}
}
