.prof-garant {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
  margin-block-end: clamp(0rem, 5.1618rem + -7.0588vw, 60px);
  &__title {
    grid-column: 1 / span 4;
    grid-row: 1 / span 1;
  }
  &__list {
    grid-column: 5 / -1;
    grid-row: 1 / span 1;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__item {
    svg {
      inline-size: 40px;
      block-size: 40px;
      margin-block-end: 15px;
      fill: var(--clr-accent);
    }
  }
  &__heading {
    margin-block-end: 10px;
  }
  .button {
    grid-column: 1 / -1;
    grid-row: 2 / span 1;
    justify-self: end;
  }
}

@media (max-width: 768px) {
  .prof-garant {
    column-gap: 0;
    &__title {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
    }
    &__list {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      column-gap: clamp(15px, 0.2679rem + 3.3482vw, 30px);
      .text {
        font-size: 16px;
      }
    }
    .button {
      grid-row: 3 / span 1;
      justify-self: center;
    }
  }
}
