.prof-categ-intro {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
  margin-block-end: clamp(40px, 1.5588rem + 4.7059vw, 80px);
  &__desc {
    grid-column: 1 / span 6;
  }
  &__title {
    margin-block-end: 20px;
  }
  &__text {}
  &__imgs {
    justify-self: end;
    grid-column: 7 / -1;
    margin-block-end: 0;
  }
}

@media (max-width: 768px) {
  .prof-categ-intro {
    column-gap: unset;
    &__desc {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
    }
    &__imgs {
      justify-self: center;
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
  }
}
