.prof-tov-wrap {
  margin-block-end: clamp(70px, 3.4338rem + 4.7059vw, 110px);
}

.prof-tov-block {
  display: flex;
  gap: 0 clamp(20px, 0.3088rem + 4.7059vw, 60px);
  &__item {
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    gap: clamp(15px, 0.5846rem + 1.7647vw, 30px) 0;
    max-inline-size: clamp(214px, 7.3515rem + 30.1176vw, 470px);
  }
  &__item:hover {
    .prof-tov-block__title a {
      color: var(--clr-accent);
    }
  }
  &__item.first {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 0;
    max-inline-size: unset;
  }
  &__item:nth-child(even) {
    .prof-tov-block__desc {
      margin-inline-start: clamp(0px, -1.4706rem + 7.3529vw, 50px);
    }
  }
  &__item:nth-child(2n+3) {
    .prof-tov-block__img {
      order: 1;
    }
    .prof-tov-block__desc {
      order: 0;
    }
  }
  &__img {
    clip-path: polygon(0 0, calc(100% - 12%) 0, 100% 100%, 12% 100%);
  }
  &__desc {
    display: grid;
    gap: 10px 0;
  }
  &__title {
    font-size: clamp(18px, 0.8426rem + 1.4118vw, 30px);
    font-weight: var(--fw-m);
    line-height: 1.3;
    a {
      color: inherit;
      transition: color var(--tr);
    }
  }
  &__title-main {
    font-size: clamp(1.5rem, 1.2176rem + 1.4118vw, 2.25rem);
  }
  &__text {
    font-size: clamp(12px, 0.6088rem + 0.7059vw, 18px);
  }
  &__text>p+p {
    margin-block-start: 20px;
  }
  &__btn {
    justify-self: end;
    display: flex;
    align-items: center;
    gap: 0 5px;
    color: var(--clr-red);
    font-size: clamp(16px, 0.9529rem + 0.2353vw, 18px);
    line-height: 20px;
    text-transform: uppercase;
    svg {
      inline-size: 20px;
      block-size: 20px;
      fill: currentColor;
      transition: transform var(--tr);
    }
  }
  &__btn:hover {
    svg {
      transform: translateX(10px);
    }
  }
}

@media (max-width: 768px) {
  .prof-tov-wrap {
    margin-inline: 15px;
  }
  .prof-tov-block {
    display: grid;
    gap: 20px 0;
    &__item {
      gap: 20px 0;
      max-inline-size: unset;
    }
    &__item.first {
      justify-content: unset;
      align-items: center;
    }
    &__item:nth-child(even) {
      .prof-tov-block__desc {
        margin-inline-start: unset;
      }
    }
    &__item:nth-child(2n+3) {
      .prof-tov-block__img {
        order: unset;
      }
      .prof-tov-block__desc {
        order: unset;
      }
    }
    &__img {
      max-inline-size: 214px;
    }
  }
}
