.container {
  --p-i: 15px;
  inline-size: min(var(--max-container) + (var(--p-i) * 2), 100%);
  margin: 0 auto;
  padding-inline: var(--p-i);
}

// @media (max-width: 768px) {
//   .container {
//     --p-i: 15px;
//   }
// }
.auto-grid {
  --min-w: 250px;
  --max-w: 1fr;
  --c-g: 30px;
  --r-g: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(var(--min-w), 100%), var(--max-w)));
  column-gap: var(--c-g);
  row-gap: var(--r-g);
}

.pb-before-footer {
  padding-block-end: var(--pb-before-footer);
}

.mobile-menu-overlay {
  position: fixed;
  z-index: 1;
  background-color: hsla(0, 0%, 0%, 0);
  transition: background-color var(--tr);
}

.mobile-menu-overlay.active {
  inset: 0;
  background-color: hsla(0, 0%, 0%, 0.5);
  cursor: pointer;
}

.pos-r {
  position: relative;
}

.p0 {
  padding: 0;
}

.mt30 {
  margin-block-start: 30px;
}

.m0 {
  margin: 0;
}

.mb0 {
  margin-block-end: 0;
}

.mb10 {
  margin-bottom: 10px;
}

.mb15 {
  margin-bottom: 15px;
}

.mb20 {
  margin-bottom: 20px;
}

.mb30 {
  margin-bottom: clamp(20px, 1.0147rem + 1.1765vw, 30px);
}

.mb40 {
  margin-bottom: 40px;
}

.mt60 {
  margin-top: clamp(30px, 1.17rem + 3.53vw, 60px);
}

.mb60 {
  margin-bottom: clamp(30px, 1.17rem + 3.53vw, 60px);
}

.mb90 {
  margin-bottom: 90px;
}

.mb100 {
  margin-bottom: 100px;
}

.clr-white {
  color: var(--clr-white);
}

.bg-white {
  background-color: var(--clr-white);
}

.uc {
  text-transform: uppercase;
}

.uc-none {
  text-transform: none;
}

.tac {
  text-align: center;
}

.fixed {
  overflow-y: hidden;
}

.noscroll {
  position: fixed;
  top: var(--st, 0);
  inline-size: 100%;
  overflow-y: scroll;
}

.ofxh {
  overflow-x: hidden;
  overflow-y: auto;
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

.grow-1 {
  flex-grow: 1;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

//------- for testing ----------------------------
.space {
  display: grid;
  justify-content: center;
  align-items: center;
  block-size: 1000px;
  // background-color: lightgrey;
  // border: 5px solid grey;
}

.btns-test-block {
  display: flex;
  gap: 40px;
  padding: 50px;
}

.btns-test-block.dark {
  background-color: var(--clr-gray);
}

.map {
  margin-block-end: 40px;
  counter-reset: list;
  &__item {
    display: block;
    padding: 5px 0 5px 20px;
    color: var(--clr-text);
  }
  &__item:hover {
    background-color: hsl(0 100% 50% / .1);
  }
  &__item::before {
    counter-increment: list;
    content: counter(list)'. ';
  }
}
