.art-potreb-complect {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
  margin-block-end: clamp(60px, 3.2794rem + 2.3529vw, 80px);
  padding-block-start: clamp(55px, 3.0846rem + 1.7647vw, 70px);
  &__img {
    grid-column: 1 / span 6;
  }
  &__desc {
    grid-column: 7 / span 6;
    margin-block-start: 170px;
  }
  &__title {
    margin-block-end: clamp(30px, 1.1691rem + 3.5294vw, 60px);
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: clamp(30px, 1.6397rem + 1.1765vw, 40px);
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__item {}
  &__item::before {
    content: '';
    display: block;
    inline-size: 38px;
    block-size: 10px;
    transform: skew(20deg);
    margin-block-end: 10px;
    background-color: var(--clr-accent);
  }
}

@media (max-width: 1024px) {
  .art-potreb-complect {
    &__desc {
      margin-block-start: 0;
    }
  }
}

@media (max-width: 767px) {
  .art-potreb-complect {
    grid-template-columns: 1fr;
    column-gap: 0;
    row-gap: 40px;
    &__img {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      max-inline-size: 570px;
    }
    &__desc {
      justify-self: unset;
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
  }
}
