.art-potreb-benefits {
  overflow: hidden;
  position: relative;
  inline-size: 100%;
  padding-block: clamp(175px, 0.9375rem + 50vw, 600px) clamp(175px, 7.9963rem + 14.7059vw, 300px);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  &__img {
    .svg-top {
      position: absolute;
      z-index: 1;
      inset-inline-start: 0;
      inset-block-start: -1px;
    }
    .svg-btm {
      position: absolute;
      z-index: 1;
      inset-inline-start: 0;
      inset-block-end: -1px;
    }
  }
  &__img::after {
    content: '';
    position: absolute;
    inset: 0;
    background-image: linear-gradient(0deg, rgba(31, 46, 54, 0.86) 51.13%, rgba(64, 83, 93, 0.17) 78.29%);
    background-attachment: fixed;
  }
  .container {
    position: relative;
    z-index: 1;
  }
  &__inner {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
    color: var(--clr-white);
  }
  &__title {
    grid-column: 1 / span 5;
  }
  &__list {
    grid-column: 6 / span 7;
    padding-inline-start: 0;
    margin-block: 0;
    list-style: none;
  }
  &__item:not(:last-child) {
    margin-block-end: clamp(30px, 1.1691rem + 3.5294vw, 60px);
  }
  &__heading {
    margin-block-end: 20px;
  }
  &__text {}
}

@media (max-width: 767px) {
  .art-potreb-benefits {
    &__inner {
      grid-template-columns: 1fr;
      column-gap: 0;
      row-gap: 30px;
    }
    &__title {
      grid-column: 1 / -1;
      margin-block-end: 0;
    }
    &__list {
      grid-column: 1 / -1;
    }
  }
}

@media (max-width: 428px) {
  .art-potreb-benefits {
    background-position: 35% 0;
  }
}
