@font-face {
  font-family: 'icomoon';
  src: url('../fonts/icomoon.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/UbuntuLight.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/UbuntuRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/UbuntuMedium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/UbuntuBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Exo2";
  src: url("../fonts/Exo2Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5 {
  font-family: var(--ff-prim);
  font-weight: var(--fw-m);
  line-height: 1.3;
}

h1,
.h1 {
  margin-block-end: 40px;
  margin-block-end: clamp(30px, calc(1.64rem + 1.18vw), 40px);
  font-size: 48px;
  font-size: clamp(28px, calc(1.28rem + 2.35vw), 48px);
  // text-transform: uppercase;
}

h2,
.h2 {
  margin-block-end: 20px;
  font-size: 36px;
  font-size: clamp(24px, calc(1.22rem + 1.41vw), 36px);
  text-transform: uppercase;
}

h3,
.h3 {
  font-size: 30px;
  font-size: clamp(20px, calc(1.01rem + 1.18vw), 30px);
}

h4,
.h4 {
  font-size: 24px;
  font-size: clamp(18px, 0.98rem + 0.71vw, 24px);
}

h5,
.h5 {
  font-size: 16px;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-car:before {
  content: "\e900";
}
