.filter-wrap {
  position: relative;
  z-index: 2;
  margin-block-end: 60px;
  padding: clamp(30px, 1.1691rem + 3.5294vw, 60px) clamp(20px, 0.3088rem + 4.7059vw, 60px);
  background-color: var(--clr-bg-gray);
}

.filter {
  --input-h: 46px;
  --tr: .3s ease-in-out;
  --skew: 12px;
  display: flex;
  &__groups {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 10px;
  }
  &__group {
    position: relative;
    z-index: 0;
    label {
      display: none;
    }
  }
  &__group.active {
    z-index: 1;
  }
  &__group:not(:nth-last-child(2)) {
    margin-inline-end: 10px;
  }
  &__group:nth-last-child(2) {
    margin-inline-end: -5px;
    .filter__input {
      clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, 0% 100%);
    }
    input {
      clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, 0% 100%);
    }
    svg {
      inset-inline-end: 15px;
    }
  }
  &__input {
    position: relative;
    block-size: var(--input-h);
    padding: 1px;
    background-color: var(--clr-border);
    input {
      display: block;
      inline-size: 100%;
      block-size: 100%;
      padding-inline: 10px 35px;
      background-color: var(--clr-white);
      font-size: 14px;
      line-height: 1.3;
      border: none;
    }
    input.active {
      background-color: var(--clr-bg-gray);
    }
    input::placeholder {
      color: var(--clr-text);
    }
    input:focus-visible::placeholder {
      color: transparent;
    }
    input:focus-visible {
      outline: none;
    }
    svg {
      position: absolute;
      inset-inline-end: 10px;
      inset-block-start: 50%;
      transform: translateY(-50%) rotate(90deg);
      inline-size: 18px;
      block-size: 18px;
      fill: var(--clr-accent);
      cursor: pointer;
      transition: transform var(--tr);
    }
    svg.active {
      transform: translateY(-50%) rotate(-90deg);
    }
  }
  &__select-wrap {
    overflow: hidden;
    position: absolute;
    inset-inline-start: 0;
    inset-block-start: var(--input-h);
    // transform: translateY(100%);
    inline-size: 100%;
    max-block-size: 0;
    transition: max-block-size .3s ease-in-out;
  }
  &__select-wrap.active {
    max-block-size: 200px;
  }
  &__select {
    overflow-x: hidden;
    overflow-y: auto;
    max-block-size: 147px;
    padding: 10px;
    background-color: var(--clr-white);
    border-inline-end: 1px solid var(--clr-border);
    border-inline-start: 1px solid var(--clr-border);
    border-block-end: 1px solid var(--clr-border);
  }
  &__select::-webkit-scrollbar {
    width: 2px;
  }
  &__select::-webkit-scrollbar-track {
    background-color: var(--clr-white);
    border-radius: 100vw;
  }
  &__select::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent);
    border-radius: 100vw;
  }
  &__list {
    margin: 0;
    padding: 0 !important;
    list-style: none;
  }
  &__list-item {
    margin-block-start: 0 !important;
    font-size: 14px;
    text-transform: uppercase;
    cursor: pointer;
    transition: color var(--tr);
  }
  &__list-item.hidden {
    display: none;
  }
  &__list-item:hover {
    color: var(--clr-red);
  }
  &__list>li+li {
    margin-block-start: 15px !important;
  }
  .button--filter {
    --h: var(--input-h);
    --pi: 23px;
    position: relative;
    z-index: 0;
    .name {
      display: none;
    }
    svg {
      inline-size: 18px;
      block-size: 18px;
    }
  }
}

@media (max-width: 1024px) {
  .filter {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    &__group {
      margin-inline-end: 0px !important;
    }
    &__group:nth-last-child(2) {
      margin-inline-end: unset;
      .filter__input {
        clip-path: unset;
      }
      input {
        clip-path: unset;
      }
      svg {
        inset-inline-end: 10px;
      }
    }
    &__input {
      input {
        font-size: 16px;
      }
    }
    .button--filter {
      justify-self: center;
      --pi: 30px;
      .name {
        display: block;
      }
      svg {
        display: none;
      }
    }
  }
}

@media (max-width: 680px) {
  .filter {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 550px) {
  .filter {
    grid-template-columns: 1fr;
    column-gap: 0;
    .button--filter {
      justify-self: center;
    }
  }
}
