.contacts-form-wrap {
  padding-block-start: 330px;
  // padding-block: 330px var(--pb-before-footer);
  padding-block: clamp(125px, calc(2.99rem + 24.12vw), 330px) var(--pb-before-footer);
  padding-block: clamp(125px, calc(2.99rem + 24.12vw), 330px) 0;
  background-image: url('../img/svg/misc/triangle-down.svg');
  background-attachment: scroll;
  background-size: 100.1% auto;
  background-position: center top;
  background-repeat: no-repeat;
  // background-image: url('../img/svg/misc/triangle-down.svg'), url('../img/misc/contacts-form-bg.webp');
  // background-attachment: scroll, fixed;
  // background-size: 100.1% auto, cover;
  // background-position: center top, center center;
  // background-repeat: no-repeat;
}

.contacts-form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  &__desc {
    inline-size: min(470px, 100%);
    color: var(--clr-white);
  }
}

@media (max-width: 1024px) {
  .contacts-form {
    .form-contacts {
      .form-top {
         :nth-child(2),
         :nth-child(3) {
          grid-column: 1 / -1;
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .contacts-form {
    grid-template-columns: 1fr;
    column-gap: unset;
    row-gap: 30px;
    .form-contacts {
      .form-top {
         :nth-child(2),
         :nth-child(3) {
          grid-column: unset;
        }
      }
    }
  }
}

@media (max-width: 560px) {
  .contacts-form {
    .form-contacts {
      .form-top {
         :nth-child(2),
         :nth-child(3) {
          grid-column: 1 / -1;
        }
      }
    }
  }
}
