.header {
  position: sticky;
  z-index: 2;
  top: -30px;
  background-color: var(--clr-white);
  &__top {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    block-size: 30px;
  }
  .container {
    block-size: 100%;
  }
  &__top-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 20px;
    block-size: 100%;
  }
  &__bottom {
    block-size: 80px;
    block-size: clamp(60px, -5.02rem + 13.70vw, 80px);
    background-color: var(--clr-bg-gray);
  }
  &__bottom-inner {
    // outline: 1px solid;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    block-size: 100%;
  }
  &__bottom-btns {
    display: flex;
    .btn--icon {
      --w: 76px;
      --w: clamp(57px, -4.77rem + 13.01vw, 76px);
      --h: 40px;
      --h: clamp(30px, -2.51rem + 6.85vw, 40px);
      svg {
        inline-size: 24px;
        block-size: 24px;
        inline-size: clamp(17px, -2.01rem + 4.79vw, 24px);
        block-size: clamp(17px, -2.01rem + 4.79vw, 24px);
      }
    }
  }
  &__logo {
    aspect-ratio: 5.6631 / 1;
    inline-size: 246px;
    inline-size: clamp(175px, -20.19rem + 48.63vw, 246px);
    block-size: auto;
  }
  &__search-wrap {
    position: absolute;
    z-index: -1;
    inset-inline-start: 0;
    inset-block-start: 100%;
    inline-size: 100%;
    visibility: hidden;
    background-color: var(--clr-white);
    opacity: 0;
    transform: translateY(-20px);
    transition: transform var(--tr), opacity var(--tr), visibility 0s linear .3s;
    .input {
      padding-inline-end: 50px;
    }
    .input::placeholder {
      color: var(--clr-border);
    }
    .input:focus-visible::placeholder {
      color: transparent;
    }
    svg {
      position: absolute;
      inset-inline-end: 20px;
      inset-block-start: 50%;
      transform: translateY(-50%);
      inline-size: 24px;
      block-size: 24px;
      fill: var(--clr-accent);
    }
    svg.close {
      cursor: pointer;
    }
    svg.hidden {
      display: none;
    }
  }
  &__search-wrap.active {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
    transition: transform var(--tr), opacity var(--tr), visibility 0s linear 0s;
  }
  &__search-group {
    position: relative;
  }
  &__search-input-wrap {
    position: relative;
  }
  &__hints {
    overflow-y: auto;
    overflow-x: hidden;
    max-block-size: 160px;
    margin-block-start: 0px;
    border-inline-end: 1px solid var(--clr-border);
    border-block-end: 1px solid var(--clr-border);
    border-inline-start: 1px solid var(--clr-border);
    transform: translateY(-1px);
  }
  &__hints::-webkit-scrollbar {
    width: 3px;
  }
  &__hints::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 100vw;
  }
  &__hints::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent);
    border-radius: 100vw;
  }
  &__hints-list {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__hints-item {
    display: flex;
    align-items: center;
    block-size: 40px;
    padding-inline: 20px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color var(--tr);
  }
  &__hints-item:hover {
    background-color: var(--clr-bg-gray);
  }
}

.cabinet {
  display: flex;
  align-items: center;
  gap: 5px;
  block-size: 100%;
  color: var(--clr-text);
  font-size: 14px;
  line-height: 1.3;
  transition: color var(--tr);
  svg {
    inline-size: 16px;
    block-size: 16px;
    fill: var(--clr-accent);
  }
}

.cabinet:hover {
  color: var(--clr-accent);
}

@media (max-width: 1000px) {
  .header {
    top: 0px;
    inline-size: 100%;
    transition: inline-size var(--tr);
    &__top {
      position: absolute;
      inset-inline-start: 0;
      inset-block-start: 100%;
      inline-size: 100%;
      block-size: 73px;
      padding-block: 25px 30px;
      background-color: var(--clr-white);
      border-block-start: 1px solid var(--clr-bg-gray);
      transform: translateX(-100%);
      transition: transform var(--tr);
    }
    &__top.active {
      transform: translateX(0%);
    }
    &__top-inner {
      flex-direction: row-reverse;
    }
    &__search-wrap {
      inset-block-start: 0;
      z-index: 3;
      transform: unset;
      transition: unset;
      padding: 30px 15px;
      padding: 30px 0px;
      background-color: var(--clr-white);
      border: 1px solid var(--clr-border);
    }
    &__search-group {
      margin-inline: 15px;
    }
    &__search-label {
      margin-inline-start: 15px;
      margin-block-end: 20px;
      font-size: 20px;
      font-weight: var(--fw-m);
    }
    &__hints {
      position: absolute;
      background-color: var(--clr-white);
      inline-size: calc(100% - 30px);
      max-block-size: 110px;
      margin-inline: 15px;
      transform: unset;
    }
    &__bottom {
      block-size: 40px;
      background-color: var(--clr-white);
      // border-block-end: 1px solid var(--clr-border);
      box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.05);
    }
    &__bottom-inner {
      position: static;
      justify-content: end;
    }
    &__logo {
      inline-size: 137px;
    }
    &__bottom-btns {
      position: fixed;
      z-index: 3;
      inset-inline-start: 0;
      inset-block-end: 135px;
      inset-block-end: 10vh;
      display: flex;
      justify-content: center;
      align-items: center;
      inline-size: 90%;
      block-size: 80px;
      background-color: #ccc;
      transform: translateX(-100%);
      transition: transform var(--tr);
      .btn--icon {
        --w: 76px;
        --h: 40px;
        svg {
          inline-size: 24px;
          block-size: 24px;
        }
      }
    }
    &__bottom-btns.active {
      transform: translateX(0);
    }
  }
  .header.active {
    inline-size: 90%;
    transition: inline-size var(--tr);
  }
}
