.art-potreb-elem2 {
  background-color: var(--clr-bg-gray);
  padding-block-end: 100px;
  &__inner {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
  }
  &__img {
    grid-column: 1 / span 8;
    grid-row: 1 / span 1;
  }
  &__title {
    grid-column: 4 / span 4;
    grid-row: 2 / span 1;
  }
  &__text {
    grid-column: 8 / span 4;
    grid-row: 2 / span 1;
  }
}

@media (max-width: 1024px) {
  .art-potreb-elem2 {
    &__inner {
      grid-template-columns: 1fr;
      column-gap: 0;
      row-gap: 20px;
    }
    &__img {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      max-inline-size: 470px;
    }
    &__title {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      margin-block-end: 0;
    }
    &__text {
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
    }
  }
}
