.imgs-couple {
  display: grid;
  max-inline-size: 470px;
  aspect-ratio: 2.1559 / 1;
  inline-size: 100%;
  margin-block-end: clamp(20px, 0.54rem + 3.53vw, 50px);
  &__img {
    --h: 88.6153%;
    --skew: calc(var(--h) / 10);
    aspect-ratio: 1.3402 / 1;
    inline-size: 55.3191%;
    block-size: var(--h);
    grid-column: 1;
    grid-row: 1;
    clip-path: polygon(0 0, calc(100% - var(--skew)) 0, 100% 100%, var(--skew) 100%);
    visibility: hidden;
  }
  &__img:first-child {
    position: relative;
    z-index: 1;
    place-self: start;
  }
  &__img:last-child {
    position: relative;
    z-index: 0;
    place-self: end;
  }
}
