.environment {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: min-content 1fr;
  column-gap: 30px;
  padding-block-end: clamp(60px, 1.75rem + 10vw, 145px);
  &__imgs {
    grid-column: 1 / span 6;
    grid-row: 1 / span 2;
  }
  &__title {
    grid-column: 7 / span 6;
    grid-row: 1 / span 1;
  }
  &__desc {
    grid-column: 7 / span 6;
    grid-row: 2 / span 1;
  }
  &__desc>p+p {
    margin-block-start: 20px;
  }
}

@media (max-width: 768px) {
  .environment {
    column-gap: unset;
    &__imgs {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      justify-self: center;
    }
    &__title {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
    &__desc {
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
    }
  }
}
